import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { AlertType, CALIMA_DANGER, ConfirmationView, InputGroupInline, InputType, ProcessBar } from 'summer';
import { promisedAlert } from '../../../../utilities/vinculo.util';

interface VinculoRemoveFields {
  cpf?: string;
}

const VinculoRemoveConfirmationView: FC<ConfirmationView> = props => {
  const formProps = useForm<VinculoRemoveFields>({ mode: 'onBlur' });

  const handleSubmit = () => {
    const values = formProps.getValues();
    if (values.cpf === props.selectedItem.cpf) {
      props.done(true);
    } else {
      promisedAlert('O CPF no corresponde ao trabalhador.', AlertType.ERROR);
    }
  };

  const handleCancel = () => props.done(false);

  return (
    <>
      <Row>
        <Col className="text-center font-weight-bold mb-2">ATENO: Este procedimento  irreversvel!</Col>
      </Row>
      <Row>
        <Col className="text-center font-weight-bold mb-3" style={{ color: CALIMA_DANGER }}>
          Trabalhador: {props.selectedItem.label}
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            id="cpf"
            label="CPF"
            type={InputType.CPF}
          />
        </Col>
      </Row>
      <ProcessBar handleSubmit={formProps.handleSubmit(handleSubmit)} handleClose={handleCancel} btnSubmitIcon="check" btnSubmitLabel="Confirmar" />
    </>
  );
};

export default VinculoRemoveConfirmationView;
