import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import {
  Accordion,
  AccordionItem,
  CALIMA_DANGER,
  compare,
  DetailEditForm,
  DetailEditViewFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  onlyNumber,
  parseDateToRef,
  parseLastDay,
  toDate,
} from 'summer';
import useCepManager from '../../../../hooks/useCepManager';
import { CepBairro } from '../../../../models/dtos/ger/cepBairro';
import { CepLocalidade } from '../../../../models/dtos/ger/cepLocalidade';
import { CepLogradouro } from '../../../../models/dtos/ger/cepLogradouro';
import { CepTitulo } from '../../../../models/dtos/ger/cepTitulo';
import { CepUF } from '../../../../models/dtos/ger/cepUF';
import { VinculoTrabalhoIntermitente } from '../../../../models/dtos/mfp/vinculoTrabalhoIntermitente';
import {
  FORA_ESTAB_INFORMADO_GRUPO,
  INDICATIVO_PRESTACAO_TRABALHO_ENUM_VALUES,
} from '../../../../models/enumerated/mfp/indicativoPrestacaoTrabalhoEnum';
import { verificarQuadroHorarioESocial } from '../../../../services/mfp.service';
import { condicional, getItemAtual } from '../../../../utilities/vinculo.util';
import CepBairroIS from '../../../components/ger/cepBairroIS/CepBairroIS';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepLogradouroIS from '../../../components/ger/cepLogradouro/CepLogradouroIS';
import CepTituloIS from '../../../components/ger/cepTituloIS/CepTituloIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';
import QuadroHorarioIS from '../../../components/mfp/quadroHorarioIS/QuadroHorarioIS';

interface ScreenFields {
  localTrabalhoBairro?: CepBairro;
  localTrabalhoCep?: string;
  localTrabalhoCepFmt?: CepLogradouro;
  localTrabalhoCidade?: CepLocalidade;
  localTrabalhoComplemento?: string;
  localTrabalhoLogradouro?: string;
  localTrabalhoNumero?: string;
  localTrabalhoTipoLogradouro?: CepTitulo;
  localTrabalhoUf?: CepUF;
}

const VinculoTrabalhoIntermitenteEditView: FC<DetailEditViewFormProps<VinculoTrabalhoIntermitente>> = props => {
  const formProps = useForm<VinculoTrabalhoIntermitente & ScreenFields>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const cepManager = useCepManager(formProps, {
    bairro: 'localTrabalhoBairro',
    cep: 'localTrabalhoCep',
    localidade: 'localTrabalhoCidade',
    nome: 'localTrabalhoLogradouro',
    titulo: 'localTrabalhoTipoLogradouro',
    uf: 'localTrabalhoUf',
  });

  const [exibirAlertaHorario, setExibirAlertaHorario] = useState(false);
  const [isLocalTrabalhoDisabled, setIsLocalTrabalhoDisabled] = useState(false);
  const [isLocalTrabalhoRequired, setIsLocalTrabalhoRequired] = useState(true);

  useEffect(
    () => {
      formProps.register({ name: 'localTrabalhoCep' });

      setTimeout(() => {
        const parentValues = props.parentFormProps.getValues();
        const values = formProps.getValues();

        if ((isEmpty(props.selectedItem) || isEmpty(props.selectedItem.id)) && parentValues.quadrosHorarios.length > 0) {
          formProps.setValue('quadroHorario', getItemAtual(parentValues.quadrosHorarios).quadroHorario);
        }

        let codigoConvocacao = 1;

        if (parentValues.trabalhosIntermitentes.length > 0) {
          codigoConvocacao = Math.max(...parentValues.trabalhosIntermitentes.map(item => parseInt(item.codigoConvocacao, 10))) + 1;
        }

        formProps.setValue('codigoConvocacao', codigoConvocacao);

        setIsLocalTrabalhoDisabled(values.indicativoPrestacaoTrabalho !== FORA_ESTAB_INFORMADO_GRUPO.value);
        setIsLocalTrabalhoRequired(values.indicativoPrestacaoTrabalho === FORA_ESTAB_INFORMADO_GRUPO.value);

        verificarQuadroHorario();
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const verificarQuadroHorario = () => {
    const values = formProps.getValues();
    if (values.quadroHorario) {
      verificarQuadroHorarioESocial(values.quadroHorario, {
        thenFunction: res => {
          if (!isEmpty(res)) {
            setExibirAlertaHorario(true);
            formProps.setValue('descricaoJornadaTrabalho', res);
          }
        },
      });
    }
  };

  const clearLocalTrabalho = () => {
    formProps.setValue([
      { localTrabalhoBairro: null },
      { localTrabalhoCep: null },
      { localTrabalhoCepFmt: null },
      { localTrabalhoCidade: null },
      { localTrabalhoComplemento: null },
      { localTrabalhoLogradouro: null },
      { localTrabalhoNumero: null },
      { localTrabalhoTipoLogradouro: null },
      { localTrabalhoUf: null },
    ]);
  };

  const localTrabalhoChange = value => {
    setIsLocalTrabalhoDisabled(value !== FORA_ESTAB_INFORMADO_GRUPO.value);
    setIsLocalTrabalhoRequired(value === FORA_ESTAB_INFORMADO_GRUPO.value);
    clearLocalTrabalho();
  };

  const dataInicioChange = () => {
    const values = formProps.getValues();
    if (values.dtInicio) {
      const data = formatDate(parseLastDay(parseDateToRef(toDate(values.dtInicio))));
      formProps.setValue([{ dtFim: data }, { dtPrevistaPagamento: data }]);
    }
  };

  const handleSubmit = (data, validateExtrasFunc) => {
    data.localTrabalho = {
      ...(props.selectedItem ? props.selectedItem.localTrabalho || {} : {}),
      bairro: data.localTrabalhoBairro,
      cep: data.localTrabalhoCep,
      cidade: data.localTrabalhoCidade,
      complemento: data.localTrabalhoComplemento,
      logradouro: data.localTrabalhoLogradouro,
      numero: data.localTrabalhoNumero,
      tipoLogradouro: data.localTrabalhoTipoLogradouro,
      uf: data.localTrabalhoUf,
    };

    [
      'localTrabalhoBairro',
      'localTrabalhoCep',
      'localTrabalhoCepFmt',
      'localTrabalhoCidade',
      'localTrabalhoComplemento',
      'localTrabalhoLogradouro',
      'localTrabalhoNumero',
      'localTrabalhoTipoLogradouro',
      'localTrabalhoUf',
    ].forEach(key => {
      delete data[key];
    });

    props.handleSubmit(data, validateExtrasFunc);
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData.dtInicio) && compare(formData.dtInicio, parentData.dataAdmissao) < 0) {
      erros.push('A Data Inicial no pode ser inferior  Data de Admisso');
    }

    if (!isEmpty(formData.dtInicio) && compare(formData.dtFim, formData.dtInicio) < 0) {
      erros.push('A Data Final no pode ser inferior  Data Inicial');
    }

    if (!isEmpty(formData.dtPrevistaPagamento) && compare(formData.dtPrevistaPagamento, parentData.dataAdmissao) < 0) {
      erros.push('A Data Prevista Pagamento no pode ser inferior  Data de Admisso');
    }

    if (parseInt(formData.codigoConvocacao, 10) < 1) {
      erros.push('Cdigo de Convocao - Campo no pode ser inferior a 1.');
    }

    parentData.trabalhosIntermitentes.forEach(cadastro => {
      const isInsert = props.selectedItem === undefined && isEmpty(formData.id) && isEmpty(cadastro.id);
      if (formData.id !== cadastro.id || isInsert) {
        if (!isEmpty(formData.dtInicio) && compare(formData.dtInicio, cadastro.dtInicio) >= 0 && compare(formData.dtInicio, cadastro.dtFim) < 1) {
          erros.push('A Data Inicial no pode estar num perodo de trabalho intermitente j cadastrado');
        }

        if (!isEmpty(formData.dtFim) && compare(formData.dtFim, cadastro.dtInicio) >= 0 && compare(formData.dtFim, cadastro.dtFim) < 1) {
          erros.push('A Data Final no pode estar num perodo de trabalho intermitente j cadastrado');
        }

        if (!isEmpty(formData.dtInicio) && !isEmpty(formData.dtFim)) {
          const checkA = compare(cadastro.dtInicio, formData.dtInicio) >= 0 && compare(cadastro.dtInicio, formData.dtFim) < 1;
          const checkB = compare(cadastro.dtFim, formData.dtInicio) >= 0 && compare(cadastro.dtFim, formData.dtFim) < 1;

          if (checkA || checkB) {
            erros.push('J existe trabalho intermitente cadastrado pertencente ao perodo de incio e fim informados');
          }
        }
      }
    });

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="codigoConvocacao"
            label="Cdigo de Convocao"
            isPrependLabel={true}
            formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 30) } }}
            tooltipProps={{ message: 'Preencher com o cdigo atribudo pela empresa que identifica a convocao para trabalho intermitente.' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtInicio"
            label="Data Inicial"
            isPrependLabel={true}
            formProps={{ ...formProps, validation: { required: true } }}
            tooltipProps={{
              message:
                'Preencher com a data de incio da prestao de trabalho intermitente. Deve ser uma data igual ou posterior ' +
                'a data de admisso do trabalhador.',
            }}
            type={InputType.DATE}
            onBlur={dataInicioChange}
          />
        </Col>
        <Col>
          <InputGroupInline
            id="dtFim"
            label="Data Final"
            isPrependLabel={true}
            formProps={{ ...formProps, validation: { required: true } }}
            tooltipProps={{
              message: 'Preencher com a data de trmino da prestao de trabalho intermitente. Deve ser uma data igual ou posterior a data inicial.',
            }}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtPrevistaPagamento"
            label="Data Prevista Pagamento"
            isPrependLabel={true}
            formProps={{ ...formProps, validation: { required: true } }}
            tooltipProps={{
              message:
                'Preencher com a data prevista para o pagamento da remunerao. Caso a convocao compreenda mais de um perodo de ' +
                'apurao, preencher com a data prevista para o pagamento da remunerao do ltimo ms. Deve ser uma data igual ou ' +
                'posterior a data de admisso do trabalhador.',
            }}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="indicativoPrestacaoTrabalho"
            label="Indicativo Prestao Trabalho"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: INDICATIVO_PRESTACAO_TRABALHO_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            onChange={localTrabalhoChange}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Accordion formProps={formProps}>
            <AccordionItem title="Local de Trabalho" disabled={isLocalTrabalhoDisabled}>
              <Row>
                <Col>
                  <CepLogradouroIS
                    id="localTrabalhoCepFmt"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired } }}
                    cepManager={cepManager}
                    label="CEP"
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CepTituloIS
                    id="localTrabalhoTipoLogradouro"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired } }}
                    label="Tipo"
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    id="localTrabalhoLogradouro"
                    label="Logradouro"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired, maxLength: 80 } }}
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col md={5}>
                  <InputGroupInline
                    id="localTrabalhoNumero"
                    label="Nmero"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired, maxLength: 10 } }}
                    isPrependLabel={true}
                  />
                </Col>
                <Col>
                  <InputGroupInline
                    id="localTrabalhoComplemento"
                    label="Complemento"
                    formProps={{ ...formProps, validation: { maxLength: 60 } }}
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CepUFIS
                    id="localTrabalhoUf"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired } }}
                    label="UF"
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CepLocalidadeIS
                    id="localTrabalhoCidade"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired } }}
                    label="Cidade"
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CepBairroIS
                    id="localTrabalhoBairro"
                    formProps={{ ...formProps, validation: { required: isLocalTrabalhoRequired } }}
                    label="Bairro"
                    isPrependLabel={true}
                  />
                </Col>
              </Row>
            </AccordionItem>
            <AccordionItem title="Horrio" open={isLocalTrabalhoDisabled}>
              <Row>
                <Col>
                  <QuadroHorarioIS
                    id="quadroHorario"
                    formProps={{ ...formProps, validation: { required: true } }}
                    label="Quadro de Horrio"
                    isPrependLabel={true}
                    onChange={verificarQuadroHorario}
                  />
                </Col>
              </Row>
              {condicional(
                exibirAlertaHorario,
                <>
                  <Row>
                    <Col>
                      <h6 className="text-center" style={{ color: CALIMA_DANGER, marginBottom: 15 }}>
                        O Quadro de horrio selecionado no  fixo, a Descrio da Jornada de Trabalho ser utilizada.
                      </h6>
                    </Col>
                  </Row>
                  <Row>
                    <Col>
                      <InputGroupInline
                        id="descricaoJornadaTrabalho"
                        label="Descrio da Jornada de Trabalho"
                        formProps={{ ...formProps, validation: { maxLength: 999 } }}
                        isPrependLabel={true}
                        tooltipProps={{ message: 'Caso o Quadro de horrio no seja fixo a Descrio da Jornada de Trabalho ser utilizada.' }}
                        type={InputType.TEXT_AREA}
                      />
                    </Col>
                  </Row>
                </>
              )}
            </AccordionItem>
          </Accordion>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoTrabalhoIntermitenteEditView;
