/* eslint-disable react-hooks/exhaustive-deps */
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import cx from 'classnames';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Col, Row } from 'reactstrap';
import {
  add,
  boolColumn,
  CALIMA_DANGER,
  Checkbox,
  cnpjFormatter,
  compare,
  cpfFormatter,
  decimalFormatter,
  DefaultSortProps,
  DetailEditForm,
  DetailEditViewFormProps,
  DetailManagerForm,
  DetailManagerFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  onlyNumber,
  OrderType,
  parseDateToRef,
  parseRefToDate,
  progressActions,
  toDate,
} from 'summer';
import { Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { VinculoSalario } from '../../../../models/dtos/mfp/vinculoSalario';
import { EMP_CONTR_PRAZO_DETERMINADO, ESTAGIARIO } from '../../../../models/enumerated/mfp/categoriaEnum';
import {
  PRAZO_DETERMINADO_COM_CLAUSULA_DE_RESCISAO,
  PRAZO_DETERMINADO_SEM_CLAUSULA_DE_RESCISAO,
} from '../../../../models/enumerated/mfp/contratoTrabalhoEnum';
import { REDUCAO_CARGA_HORARIA } from '../../../../models/enumerated/mfp/tipoAdesaoBEMEnum';
import { MENSAL, PERIODICO, TIPO_PAGAMENTO_MFP_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoPagamentoMFPEnum';
import { validarSalarioVinculo } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import { NRO_HORAS_CONTRATUAIS_MENSALISTA } from '../../../../utilities/constantes';
import {
  condicional,
  getItemAtual,
  getReferenciaAtual,
  isSalarioRepetido,
  isVinculoProfessor,
  promisedAlert,
  showLoading,
} from '../../../../utilities/vinculo.util';
import VinculoSalarioOutraRemuneracaoEditView from './VinculoSalarioOutraRemuneracaoEditView';

interface ExtraFields {
  categoriaVinculo?: string;
  reajuste?: number;
  reajusteHoras?: number;
}

const VinculoSalarioEditView: FC<DetailEditViewFormProps<VinculoSalario>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const dispatch = useDispatch();

  const formProps = useForm<VinculoSalario & ExtraFields>({
    defaultValues: { ...props.selectedItem, categoriaVinculo: props.parentFormProps.getValues().categoria },
    mode: 'onBlur',
  });

  const [modoTela, setModoTela] = useState(null);
  const [disabledEdit, setDisabledEdit] = useState(false);
  const [isMensalista, setIsMensalista] = useState(false);
  const [isPeriodico, setIsPeriodico] = useState(false);
  const [isIntermitente, setIsIntermitente] = useState(false);
  const [checkHoristaDisabled, setCheckHoristaDisabled] = useState(false);
  const [checkDiaristaDisabled, setCheckDiaristaDisabled] = useState(false);
  const [isMPDisabled, setIsMPDisabled] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      // Campo utilizado pela tela VinculoSalarioOutraRemuneracaoEditView
      formProps.register({ name: 'categoriaVinculo' });

      configTrabalhadorIntermitente();
      validarEdicao();
    }, 700);
  }, []);

  // Re-executa aps a validao do trabalhador intermitente
  useEffect(() => {
    setTimeout(() => {
      configurarSalario();
      ajustarPorTipoPagamento(false);
    }, 700);
  }, [isIntermitente]);

  const validarEdicao = () => {
    if (props.selectedItem && !isEmpty(props.selectedItem.id)) {
      const vinculo = props.parentFormProps.getValues();
      dispatch(showLoading());
      validarSalarioVinculo(vinculo.id, props.selectedItem, {
        thenFunction: result => {
          dispatch(progressActions.hideProgress());
          setDisabledEdit(result);
        },
      });
    }
  };

  const ajustarPorTipoPagamento = (ajustarHorasContratuais = true) => {
    const values = formProps.getValues();

    const mensalista = values.tipoPagamento === MENSAL.value;
    const periodico = values.tipoPagamento === PERIODICO.value;

    if (mensalista) {
      setCheckHoristaDisabled(false);
    } else if (periodico) {
      setCheckHoristaDisabled(true);
      formProps.setValue('horista', true);
      values.horista = true;
    }

    if (ajustarHorasContratuais) {
      formProps.setValue('numeroHorasContratuais', NRO_HORAS_CONTRATUAIS_MENSALISTA);
    }

    setIsMensalista(mensalista);
    setIsPeriodico(periodico);
    setCheckDiaristaDisabled(values.horista === false);

    if (!validarModoProfessor()) {
      if (values.horista) {
        setModoTela(isIntermitente ? 'horistaIntermitente' : 'horista');
      } else {
        setModoTela(isIntermitente ? 'mensalistaIntermitente' : 'mensalista');
      }
    }
  };

  const validarModoProfessor = () => {
    let result = false;
    if (isVinculoProfessor(props.parentFormProps.getValues())) {
      setModoTela('professor');
      result = true;
    }
    return result;
  };

  const configurarSalario = () => {
    validarModoProfessor();

    if (!props.selectedItem || isEmpty(props.selectedItem.nroOrdem)) {
      const vinculo = props.parentFormProps.getValues();
      const salario = formProps.getValues();
      const parametros: any[] = [{ tipoPagamento: globalReducer.globalParameter.empresaMFP.tipoPagamento }];

      if (vinculo.salarios.length > 0) {
        const salarioAtual = getItemAtual(vinculo.salarios);

        parametros.push(
          { nroOrdem: salarioAtual.nroOrdem + 1 },
          { tipoPagamento: salarioAtual.tipoPagamento },
          { numeroHorasContratuais: salarioAtual.numeroHorasContratuais },
          { valor: salarioAtual.valor },
          { numeroHorasContratuaisFixa: salarioAtual.numeroHorasContratuaisFixa },
          {
            outrasRemuneracoes: salarioAtual.outrasRemuneracoes.map(item => {
              item.id = null;
              return item;
            }),
          }
        );
      } else {
        parametros.push({ nroOrdem: 1 }, { numeroHorasContratuais: NRO_HORAS_CONTRATUAIS_MENSALISTA });
      }

      if (isEmpty(salario.data)) {
        let dataAtual = formatDate(parseRefToDate(getReferenciaAtual(globalReducer), 1));
        if (compare(dataAtual, vinculo.dataAdmissao) < 0) {
          dataAtual = vinculo.dataAdmissao;
        }
        parametros.push({ data: dataAtual });
      }

      formProps.setValue(parametros);
    }

    ajustarMP();
  };

  const configTrabalhadorIntermitente = () => {
    const vinculo: Vinculo = props.parentFormProps.getValues();
    setIsIntermitente(
      (vinculo.contratoTrabalho === PRAZO_DETERMINADO_COM_CLAUSULA_DE_RESCISAO.value ||
        vinculo.contratoTrabalho === PRAZO_DETERMINADO_SEM_CLAUSULA_DE_RESCISAO.value) &&
        vinculo.categoria === EMP_CONTR_PRAZO_DETERMINADO.value
    );
  };

  const changeTipoPagamento = () => ajustarPorTipoPagamento();

  const changeCheckHorista = () => {
    ajustarPorTipoPagamento();
    formProps.setValue('diarista', false);
  };

  const isNumeroHorasContratuaisFixa = () => formProps.getValues().numeroHorasContratuaisFixa;

  const ajustarMP = () => {
    setIsMPDisabled(true);

    const values = formProps.getValues();

    const parentValues = props.parentFormProps.getValues();
    const camposPreenchidos =
      isEmpty(parentValues.tipoAdesaoBEM) || isEmpty(parentValues.dtAcordoBEM) || isEmpty(parentValues.diasDuracaoAcordo) === false;

    if (!disabledEdit && camposPreenchidos && parentValues.tipoAdesaoBEM === REDUCAO_CARGA_HORARIA.value) {
      const dataInicial = parseRefToDate(parseDateToRef(toDate(parentValues.dtAcordoBEM)), 1);
      const dataFinal = add(dataInicial, parentValues.diasDuracaoAcordo, 'days');

      const strDataInicial = formatDate(dataInicial);
      const strDataFinal = formatDate(dataFinal);

      if (compare(values.data, strDataInicial) >= 0 && compare(values.data, strDataFinal) <= 0) {
        setIsMPDisabled(false);
        formProps.setValue('mp93620', true);
        if (!isEmpty(parentValues.percentualReducaoCargaHoraria) && isEmpty(values.id)) {
          formProps.setValue([
            { reajuste: parentValues.percentualReducaoCargaHoraria },
            { reajusteHoras: parentValues.percentualReducaoCargaHoraria },
            { motivo: 'Reduo salarial seguindo MPV-936/20' },
          ]);
        }
      }
    }
  };

  const reajustarSalario = (fator: number, loop: boolean = true) => {
    return () => {
      const values = formProps.getValues();
      const valor = parseFloat(values.valor.toString());
      const reajuste = parseFloat(values.reajuste.toString());

      const valorReajuste = valor * (reajuste / 100) * fator;
      const novoSalario = valor + valorReajuste;

      formProps.setValue('valor', parseFloat((novoSalario > 0 ? novoSalario : 0).toFixed(2)));
      if (values.mp93620 && !values.horista && loop) {
        reajustarHoras(fator, false);
      }
    };
  };

  const reajustarHoras = (fator: number, loop: boolean = true) => {
    return () => {
      const values = formProps.getValues();
      const valor = parseFloat(values.numeroHorasContratuais.toString());
      const reajuste = parseFloat(values.reajusteHoras.toString());

      const valorReajuste = valor * (reajuste / 100) * fator;
      const novasHoras = valor + valorReajuste;

      formProps.setValue('numeroHorasContratuais', parseFloat((novasHoras > 0 ? novasHoras : 0).toFixed(2)));
      if (values.mp93620 && !values.horista && loop) {
        reajustarSalario(fator, false);
      }
    };
  };

  const validateExtras = formData => {
    const erros = [];

    const vinculo = props.parentFormProps.getValues();

    if (modoTela !== 'professor') {
      if (parseFloat(formData.valor) <= 0 && vinculo.categoria !== ESTAGIARIO.value) {
        erros.push('Valor deve ser maior que zero.');
      }
      if (parseFloat(formData.numeroHorasContratuais) <= 0) {
        erros.push('Nmero de horas Mensais deve ser maior que zero.');
      }
    }

    const salarioAtual = getItemAtual(vinculo.salarios);

    if (salarioAtual && salarioAtual.id !== formData.id) {
      if (isSalarioRepetido(salarioAtual, formData)) {
        erros.push('Os valores salariais informados so idnticos ao anterior');
      }
    }

    if (vinculo.dataAdmissao && compare(vinculo.dataAdmissao, formData.data) > 0) {
      erros.push('Data do salrio no pode ser menor que a data de admisso');
    }

    return erros;
  };

  const handleSubmit = (formData, validateExtrasFunc) => {
    const vinculo = props.parentFormProps.getValues();
    const salarioAtual = getItemAtual(vinculo.salarios);

    let promise = null;

    if (salarioAtual) {
      if (
        !isVinculoProfessor(vinculo) &&
        (parseFloat(formData.numeroHorasContratuais) < parseFloat(salarioAtual.numeroHorasContratuais) ||
          parseFloat(formData.valor) < parseFloat(salarioAtual.valor))
      ) {
        promise = promisedAlert('O novo valor do salrio ou nmero de horas contratuais est inferior ao salrio atual.\nDeseja continuar?');
      }

      if (compare(formData.data, salarioAtual.data) < 0) {
        const msgA = 'A data de incluso do salrio est menor do que a data do salrio atual.\nDeseja continuar?';
        if (promise) {
          promise = promise.then(() => promisedAlert(msgA));
        } else {
          promise = promisedAlert(msgA);
        }
      }

      if (formData.tipoPagamento !== globalReducer.globalParameter.empresaMFP.tipoPagamento) {
        const msgB = 'O tipo de pagamento selecionado est diferente do selecionado na empresa.\nDeseja continuar?';
        if (promise) {
          promise = promise.then(() => promisedAlert(msgB));
        } else {
          promise = promisedAlert(msgB);
        }
      }
    }

    if (promise) {
      promise.then(() => {
        props.handleSubmit(formData, validateExtrasFunc);
      });
    } else {
      props.handleSubmit(formData, validateExtrasFunc);
    }
  };

  const outraRemuneracaoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'id',
      order: OrderType.ASC,
    },
  ];

  const outraRemuneracaoFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'cnpj',
        formatter: cnpjFormatter,
        headerStyle: { width: 160 },
        text: 'C.N.P.J.',
      },
      {
        dataField: 'cpf',
        formatter: cpfFormatter,
        headerStyle: { width: 140 },
        text: 'C.P.F.',
      },
      {
        dataField: 'valor',
        formatter: decimalFormatter,
        text: 'Valor',
      },
      boolColumn({
        dataField: 'insideInss',
        text: 'Incide INSS',
      }),
      boolColumn({
        dataField: 'insideIrrf',
        text: 'Incide IRRF',
      }),
      boolColumn({
        dataField: 'insideFgts',
        text: 'Incide FGTS',
      }),
    ],
    detailView: VinculoSalarioOutraRemuneracaoEditView,
    detailViewSize: 'large',
    formProps,
    onBeforeEdit: disabledEdit ? () => ({ async: true }) : null,
    propName: 'outrasRemuneracoes',
    title: 'Valor de Outras Remuneraes - Dados de Outros Vnculos Empregatcios',
  };

  return (
    <DetailEditForm handleSubmit={handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      {condicional(
        modoTela !== 'professor',
        <>
          <div className="d-flex">
            <div className="flex-grow-1">
              <InputGroupInline
                id="tipoPagamento"
                label="Tipo de pagamento"
                formProps={{ ...formProps, validation: { required: true } }}
                isPrependLabel={true}
                placeholder="Selecione o tipo de pagamento"
                inputDropDownProps={{ optionsProvider: TIPO_PAGAMENTO_MFP_ENUM_VALUES }}
                type={InputType.DROP_DOWN}
                onChange={changeTipoPagamento}
                disabled={disabledEdit}
              />
            </div>
            <div style={{ width: 144, marginLeft: 15, paddingTop: 10 }}>
              <Checkbox
                formProps={formProps}
                id="mp93620"
                label="MPV-936/20"
                disabled={isMPDisabled}
                labelProps={{ style: { color: CALIMA_DANGER } }}
              />
            </div>
            <div style={{ width: 110, marginLeft: 15, paddingTop: 10 }}>
              <Checkbox
                formProps={formProps}
                id="horista"
                label="Horista"
                disabled={disabledEdit || checkHoristaDisabled}
                onChange={changeCheckHorista}
              />
            </div>
            <div style={{ width: 110, marginLeft: 15, paddingTop: 10 }}>
              <Checkbox formProps={formProps} id="diarista" label="Diarista" disabled={disabledEdit || checkDiaristaDisabled} />
            </div>
          </div>
          <div className="d-flex">
            <div className="flex-grow-1">
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 100) } }}
                isPrependLabel={true}
                id="valor"
                label={['mensalista', 'mensalistaIntermitente'].indexOf(modoTela) >= 0 ? 'Valor do salrio' : 'Valor da Hora'}
                disabled={disabledEdit}
              />
            </div>
            <div style={{ width: 245, marginLeft: 20, marginRight: 10 }}>
              <InputGroupInline
                formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                isPrependLabel={true}
                id="reajuste"
                label="Reajustar (%)"
                placeholder="&nbsp;"
                type={InputType.NUMBER}
                disabled={disabledEdit}
              />
            </div>
            <div style={{ marginTop: 8 }}>
              <Button
                color="neutral-success"
                disabled={disabledEdit}
                onClick={reajustarSalario(1)}
                className={cx('btn-pill d-30 p-0 align-top', { 'btn-animated-icon btn-animated-icon--success': !disabledEdit })}
              >
                <span className="btn-wrapper--icon">
                  <FontAwesomeIcon icon={['fas', 'plus']} />
                </span>
              </Button>
              <Button
                color="neutral-danger"
                disabled={disabledEdit}
                onClick={reajustarSalario(-1)}
                className={cx('btn-pill d-30 p-0 align-top ml-2', { 'btn-animated-icon btn-animated-icon--danger': !disabledEdit })}
              >
                <span className="btn-wrapper--icon">
                  <FontAwesomeIcon icon={['fas', 'minus']} />
                </span>
              </Button>
            </div>
          </div>
          <div className="d-flex">
            <div className="flex-grow-1">
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                isPrependLabel={true}
                id="numeroHorasContratuais"
                label={['mensalista', 'mensalistaIntermitente'].indexOf(modoTela) >= 0 ? 'N Horas MENSAIS' : 'N Horas MENSAIS (ms 30 dias)'}
                type={InputType.FRACTION_OF_TIME}
                disabled={disabledEdit}
              />
            </div>
            <div style={{ width: 245, marginLeft: 20, marginRight: 10 }}>
              <InputGroupInline
                formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                isPrependLabel={true}
                id="reajusteHoras"
                label="Reajustar (%)"
                placeholder="&nbsp;"
                type={InputType.NUMBER}
                disabled={disabledEdit}
              />
            </div>
            <div style={{ marginTop: 8 }}>
              <Button
                color="neutral-success"
                disabled={disabledEdit}
                onClick={reajustarHoras(1)}
                className={cx('btn-pill d-30 p-0 align-top', { 'btn-animated-icon btn-animated-icon--success': !disabledEdit })}
              >
                <span className="btn-wrapper--icon">
                  <FontAwesomeIcon icon={['fas', 'plus']} />
                </span>
              </Button>
              <Button
                color="neutral-danger"
                disabled={disabledEdit}
                onClick={reajustarHoras(-1)}
                className={cx('btn-pill d-30 p-0 align-top ml-2', { 'btn-animated-icon btn-animated-icon--danger': !disabledEdit })}
              >
                <span className="btn-wrapper--icon">
                  <FontAwesomeIcon icon={['fas', 'minus']} />
                </span>
              </Button>
            </div>
          </div>
        </>
      )}
      {condicional(
        modoTela === 'horista' || modoTela === 'horistaIntermitente',
        <div className="d-flex" style={{ marginBottom: 15, paddingLeft: 10 }}>
          {condicional(
            isMensalista,
            <div className="flex-grow-1">
              <Checkbox
                formProps={formProps}
                id="numeroHorasContratuaisFixa"
                label="Fixar N Horas MENSAIS"
                disabled={disabledEdit}
                tooltipProps={{
                  message:
                    'Caso este campo seja marcado, o sistema ir levar em conta o valor que estiver no campo N Horas MENSAIS. ' +
                    'Caso contrrio, ele ir calcular proporcionalmente como (N Horas MENSAIS)/30 * (Nmero de dias a serem ' +
                    'considerados no clculo). O Clculo tambm ser feito de forma proporcional se houver afastamentos ou admisso ' +
                    'do vnculo dentro do perodo de clculo da respectiva referncia, ignorando esta marcao.',
                }}
              />
            </div>
          )}
          {condicional(
            isPeriodico,
            <div className="flex-grow-1">
              <Checkbox
                formProps={formProps}
                id="numeroHorasContratuaisFixa"
                label="Fixar N Horas por PERODO"
                disabled={disabledEdit}
                tooltipProps={{
                  message:
                    'Caso este campo seja marcado, o sistema ir levar em conta o valor que estiver no campo N Horas por PERODO. ' +
                    'Caso contrrio, ele ir calcular proporcionalmente como (N Horas MENSAIS)/30 * (Nmero de dias a serem ' +
                    'considerados no clculo). O Clculo tambm ser feito de forma proporcional se houver afastamentos ou admisso ' +
                    'do vnculo dentro do perodo de clculo da respectiva referncia, ignorando esta marcao.',
                }}
              />
            </div>
          )}
          {condicional(
            isNumeroHorasContratuaisFixa(),
            <div style={{ width: 440 }}>
              <Checkbox
                formProps={formProps}
                id="calcularHorasProporcionaisAdmissaoDemissao"
                label="Calcular horas proporcionais na admisso/demisso"
                disabled={disabledEdit}
                tooltipProps={{
                  message:
                    'Caso este campo seja marcado, o sistema ir calcular proporcionalmente como (N Horas MENSAIS)/30 * (Nmero de ' +
                    'dias a serem considerados no clculo) para admisso/demisso do vnculo dentro do perodo de clculo da ' +
                    'respectiva referncia.',
                }}
              />
            </div>
          )}
        </div>
      )}
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            id="data"
            label="Data de incluso do salrio"
            type={InputType.DATE}
            disabled={disabledEdit}
            onBlur={ajustarMP}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
            isPrependLabel={true}
            id="porcentagemComissaoSobreVendas"
            label="Porcentagem de comisso sobre vendas"
            type={InputType.NUMBER}
            disabled={disabledEdit}
          />
        </Col>
      </Row>
      {condicional(
        modoTela === 'horistaIntermitente' || modoTela === 'mensalistaIntermitente',
        <>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { maxLength: 128 } }}
                isPrependLabel={true}
                id="localPagamento"
                label="Local do pagamento"
                disabled={disabledEdit}
              />
            </Col>
          </Row>
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
                isPrependLabel={true}
                id="prazoPagamento"
                label="Prazo para pagamento (em dias)"
                disabled={disabledEdit}
              />
            </Col>
          </Row>
        </>
      )}
      {condicional(
        modoTela !== 'professor',
        <Row>
          <Col>
            <InputGroupInline formProps={formProps} isPrependLabel={true} id="motivo" label="Motivo da incluso do salrio" disabled={disabledEdit} />
          </Col>
        </Row>
      )}
      <Row>
        <Col id="detailManagerOutraRemuneracao">
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={outraRemuneracaoDefaultSortProps}
            {...outraRemuneracaoFormProps}
            keyField="rowIndex"
            hideAddButton={disabledEdit}
            hideEditButton={disabledEdit}
            hideRemoveButton={disabledEdit}
          />
        </Col>
      </Row>
      {condicional(
        modoTela === 'professor',
        <Row>
          <Col>
            <h6 className="text-center">ATENO: Para informar o valor do salrio de professor deve-se informar eventos do tipo 'Salrio Aula'.</h6>
          </Col>
        </Row>
      )}
      {condicional(
        disabledEdit,
        <Row>
          <Col>
            <h6 className="text-center" style={{ color: CALIMA_DANGER }}>
              ATENO: No  possvel alterar o salrio, pois o mesmo  referenciado nos clculos. Para alterar o valor, adicione um novo salrio.
            </h6>
          </Col>
        </Row>
      )}
      <div style={{ marginBottom: 20 }} />
    </DetailEditForm>
  );
};

export default VinculoSalarioEditView;
