import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/responsavelMenorAprendiz';

const ResponsavelMenorAprendizIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'nome',
            text: 'Nome do Responsvel',
          },
          {
            dataField: 'cpf',
            text: 'CPF',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'ResponsavelMenorAprendizSearchView',
      }}
    />
  );
};

export default ResponsavelMenorAprendizIS;
