import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { Col, Row } from 'reactstrap';
import { compare, DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, isEmpty, progressActions } from 'summer';
import { VinculoTomadorServico } from '../../../../models/dtos/mfp/vinculoTomadorServico';
import { existeCalculoVinculoTomadorServico } from '../../../../services/mfp.service';
import { showLoading } from '../../../../utilities/vinculo.util';
import TomadorServicoIS from '../../../components/mfp/tomadorServicoIS/TomadorServicoIS';

const VinculoTomadorServicoDetailEditView: FC<DetailEditViewFormProps<VinculoTomadorServico & { rowIndex?: number }>> = props => {
  const formProps = useForm<VinculoTomadorServico>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });
  const dispatch = useDispatch();

  const [tomadorServicoDisabled, setTomadorServicoDisabled] = useState(false);

  useEffect(
    () => {
      setTimeout(() => {
        if (props.selectedItem && props.selectedItem.id) {
          const values = formProps.getValues();
          const parentValues = props.parentFormProps.getValues();
          dispatch(showLoading());
          existeCalculoVinculoTomadorServico(
            {
              tomadorServico: {
                dtFim: values.dtFim,
                dtInicio: values.dtInicio,
                tomadorServico: {
                  id: values.tomadorServico.id,
                },
              },
              vinculo: {
                id: parentValues.id,
              },
            },
            {
              thenFunction: res => {
                dispatch(progressActions.hideProgress());
                setTomadorServicoDisabled(res);
              },
            }
          );
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData.dtFim) && compare(formData.dtInicio, formData.dtFim) > 0) {
      erros.push('A Data Final deve estar em branco ou posterior  Data Inicial.');
    }

    if (compare(parentData.dataAdmissao, formData.dtInicio) >= 0) {
      erros.push('A Data Inicial no pode ser anterior  Data de Admisso.');
    }

    for (let index = 0; index < parentData.tomadoresServico.length; index++) {
      const tomador = parentData.tomadoresServico[index];
      if (!props.selectedItem || props.selectedItem.rowIndex !== index || props.selectedItem.id) {
        if (isEmpty(tomador.dtFim)) {
          erros.push('Existe um Tomador de Servio em aberto, preencha sua Data Fim antes de inserir um novo Registro.');
          break;
        }

        const compareA = compare(formData.dtInicio, tomador.dtInicio) >= 0 && compare(formData.dtInicio, tomador.dtFim) <= 0;
        const compareB = compare(formData.dtFim, tomador.dtInicio) >= 0 && compare(formData.dtFim, tomador.dtFim) <= 0;

        if (compareA || compareB) {
          erros.push('J existe Tomador de Servio cadastrado pertencente ao perodo de incio e fim informado');
          break;
        }
      }
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <TomadorServicoIS
            id="tomadorServico"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Tomador Servio"
            isPrependLabel={true}
            disabled={tomadorServicoDisabled}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtInicio"
            label="Data Inicial"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
        <Col>
          <InputGroupInline id="dtFim" label="Data Final" formProps={formProps} isPrependLabel={true} type={InputType.DATE} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoTomadorServicoDetailEditView;
