import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/instituicaoEnsino';

const InstituicaoEnsinoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'InstituicaoEnsinoSearchView',
      }}
    />
  );
};

export default InstituicaoEnsinoIS;
