import React, { FC, useEffect, useRef } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditFormRefProps,
  EditViewProps,
  getSelectedItem,
  OrderType,
  ProcessBar,
  WindowComponent,
  Wizard,
  WizardActionFinishProps,
  WizardPage,
} from 'summer';
import { CLASS_NAME, Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { loadVinculo, registrarListasVinculo } from '../../../../utilities/vinculo.util';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import VinculoTomadorServicoDetailEditView from './VinculoTomadorServicoDetailEditView';

interface VinculoTomadorServicoEditViewProps extends EditViewProps<Vinculo> {
  onDockSubmit?: (data: any) => void;
  openViaDock?: boolean;
}

interface ScreenFields {
  vinculo?: Vinculo;
}

const VinculoTomadorServicoEditView: FC<VinculoTomadorServicoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Vinculo & ScreenFields>({ defaultValues: { tomadoresServico: [], ...selectedItem }, mode: 'onBlur' });
  const dispatch = useDispatch();

  const editFormRef = useRef<EditFormRefProps>();

  useEffect(
    () => {
      setTimeout(() => {
        registrarListasVinculo(formProps, ['tomadoresServico'], ['dataAdmissao']);
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onPageChange = currentPageId => {
    if (currentPageId === 'tomadoresServico') {
      loadVinculo(formProps, dispatch);
    }
  };

  const reset = () => {
    formProps.setValue('vinculo', null);
  };

  const onCancel = () => {
    editFormRef.current.close();
  };

  const handleSubmitViaDock = () => {
    editFormRef.current.submit(false).then(res => {
      editFormRef.current.close();
      if (props.onDockSubmit) {
        props.onDockSubmit(res);
      }
    });
  };

  const tomadorDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'dtInicio',
      order: OrderType.ASC,
    },
  ];

  const tomadorFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'tomadorServico.nome',
        text: 'Tomador',
      },
      {
        dataField: 'dtInicio',
        text: 'Data Inicial',
      },
      {
        dataField: 'dtFim',
        text: 'Data Final',
      },
    ],
    detailView: VinculoTomadorServicoDetailEditView,
    detailViewSize: 'large',
    detailViewWikiId: 1736,
    formProps,
    propName: 'tomadoresServico',
    title: 'Histrido de Tomadores de Servio',
    wikiId: 1736,
  };

  const wizardFinishProps: WizardActionFinishProps = {
    confirmButton: {
      label: 'Salvar',
    },
    onCancel: () => {
      reset();
      onCancel();
    },
    onConfirm: () => {
      editFormRef.current.submit().then(reset);
    },
  };

  const getEditView = () => (
    <Row>
      <Col>
        <DetailManagerForm
          {...props}
          defaultSortDirection={OrderType.ASC}
          defaultSorted={tomadorDefaultSortProps}
          {...tomadorFormProps}
          keyField="rowIndex"
        />
      </Col>
    </Row>
  );

  return (
    <WindowComponent title="Manuteno de Tomadores de Servio" wrapBody={props.openViaDock === true} wikiId={1736}>
      <EditForm ref={editFormRef} formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} externalSubmit={true}>
        {props.openViaDock === true ? (
          <div className="m-3">
            {getEditView()}
            <ProcessBar handleSubmit={handleSubmitViaDock} handleClose={onCancel} btnSubmitIcon="save" btnSubmitLabel="Salvar" />
          </div>
        ) : (
          <Wizard formProps={formProps} onAfterPageChange={onPageChange} finishProps={wizardFinishProps}>
            <WizardPage id="vinculo" title="Seleo do vnculo">
              <Row>
                <Col>
                  <VinculoIS id="vinculo" formProps={{ ...formProps, validation: { required: true } }} label="Vinculo" isPrependLabel={true} />
                </Col>
              </Row>
            </WizardPage>
            <WizardPage id="tomadoresServico" title="Tomadores de Servio">
              {getEditView()}
            </WizardPage>
          </Wizard>
        )}
      </EditForm>
    </WindowComponent>
  );
};

export default VinculoTomadorServicoEditView;
