import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/ger/agencia';

const AgenciaIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'numero',
            text: 'Nmero',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'banco.nome',
            text: 'Banco',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'AgenciaSearchView',
      }}
    />
  );
};

export default AgenciaIS;
