import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/planoSaude';

const PlanoSaudeIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'razaoSocial',
            text: 'Razo Social',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
          {
            dataField: 'registroANS',
            text: 'Registro ANS',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'PlanoSaudeSearchView',
      }}
    />
  );
};

export default PlanoSaudeIS;
