import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { Checkbox, DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, isEmpty, onlyNumber } from 'summer';
import { VinculoSalarioOutraRemuneracao } from '../../../../models/dtos/mfp/vinculoSalarioOutraRemuneracao';
import { CATEGORIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';

const VinculoSalarioOutraRemuneracaoEditView: FC<DetailEditViewFormProps<VinculoSalarioOutraRemuneracao>> = props => {
  const formProps = useForm<VinculoSalarioOutraRemuneracao>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('categoria', props.parentFormProps.getValues().categoriaVinculo);
      }, 500);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (isEmpty(formData.cpf) && isEmpty(formData.cnpj)) {
      erros.push('Um dos campos CPF/CNPJ deve ser preenchido.');
    }

    if (formData.cpf && formData.cnpj) {
      erros.push('Apenas um dos campos CPF/CNPJ deve ser preenchido.');
    }

    if (parseFloat(formData.valor) <= 0) {
      erros.push('O Campo Valor deve ser maior que ZERO.');
    }

    if (validarExistencia(formData.id, formData.cpf, parentData) || validarExistencia(formData.id, formData.cnpj, parentData)) {
      erros.push('Este CPF/CNPJ j foi utilizado para este salrio.');
    }

    return erros;
  };

  const validarExistencia = (id, valor, vinculoSalario) =>
    valor ? vinculoSalario.outrasRemuneracoes.find(item => (item.cpf === valor || item.cnpj === valor) && item.id !== id) !== undefined : false;

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
            isPrependLabel={true}
            id="nomeRazaoSocial"
            label="Nome / Razo Social"
            tooltipProps={{ message: 'Nome ou Razao Social do outro Vnculo Empregatcio' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={formProps}
            isPrependLabel={true}
            id="cpf"
            label="C.P.F."
            tooltipProps={{ message: 'CPF do outro Vnculo Empregatcio' }}
            type={InputType.CPF}
          />
        </Col>
        <Col>
          <InputGroupInline
            formProps={formProps}
            isPrependLabel={true}
            id="cnpj"
            label="C.N.P.J."
            tooltipProps={{ message: 'CNPJ do outro Vnculo Empregatcio' }}
            type={InputType.CNPJ}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="categoria"
            label="Categoria"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            placeholder="Selecione a Categoria"
            inputDropDownProps={{ optionsProvider: CATEGORIA_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            tooltipProps={{ message: 'Categoria do Trabalhador no outro Vnculo Empregatcio' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 100) } }}
            isPrependLabel={true}
            id="valor"
            label="Valor"
            tooltipProps={{ message: 'Valor de Remunerao no outro Vnculo Empregatcio' }}
          />
        </Col>
      </Row>
      <Row style={{ marginBottom: 20 }}>
        <Col className="text-center">
          <Checkbox formProps={formProps} id="insideInss" label="Incide p/ INSS" />
        </Col>
        <Col className="text-center">
          <Checkbox formProps={formProps} id="insideIrrf" label="Incide p/ IRRF" />
        </Col>
        <Col className="text-center">
          <Checkbox formProps={formProps} id="insideFgts" label="Incide p/ FGTS" />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoSalarioOutraRemuneracaoEditView;
