import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import {
  compare,
  currentDate,
  DetailEditForm,
  DetailEditViewFormProps,
  format,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  toDate,
} from 'summer';
import { VinculoPrevidenciaComplementar } from '../../../../models/dtos/mfp/vinculoPrevidenciaComplementar';
import { ESEP, ESFA, ESPP, ESSP, RTEP, RTFA, RTPP, RTSP } from '../../../../models/enumerated/mfp/naturezaEventoEnum';
import { getItemAtual } from '../../../../utilities/vinculo.util';
import EventoIS from '../../../components/mfp/eventoIS/EventoIS';
import PrevidenciaComplementarIS from '../../../components/mfp/previdenciaComplementarIS/PrevidenciaComplementarIS';

const VinculoPrevidenciaComplementarEditView: FC<DetailEditViewFormProps<VinculoPrevidenciaComplementar>> = props => {
  const formProps = useForm<VinculoPrevidenciaComplementar>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const naturezasValidas = [RTPP, RTFA, RTSP, RTEP, ESPP, ESFA, ESSP, ESEP];

  useEffect(
    () => {
      setTimeout(() => {
        const vinculo = props.parentFormProps.getValues();

        formProps.setValue('dataAlteracao', formatDate(currentDate()));
        formProps.setValue('dtInicio', vinculo.dataAdmissao);

        if (vinculo.previdenciasComplementar.length > 0) {
          formProps.setValue('nroOrdem', getItemAtual(vinculo.previdenciasComplementar).nroOrdem + 1);
        } else {
          formProps.setValue('nroOrdem', 1);
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData.dtFim) && compare(formData.dtInicio, formData.dtFim) === 1) {
      erros.push('A Data Final deve estar em branco ou posterior  Data Inicial.');
    }

    if (compare(parentData.dataAdmissao, formData.dtInicio) === 1) {
      erros.push('A Data Inicial no pode ser anterior  Data de Admisso.');
    }

    if (formData.previdenciaComplementar && formData.evento) {
      let previdenciaJaCadastrada = false;
      let eventoJaCadastrado = false;
      let naturezaEventoDiferente = false;

      parentData.previdenciasComplementar.forEach(previdencia => {
        const vemAntes =
          compare(formData.dtInicio, previdencia.dtInicio) < 0 && !isEmpty(formData.dtFim) && compare(formData.dtFim, previdencia.dtInicio) < 0;
        const periodoNaoCadastrado = isEmpty(previdencia.dtFim) ? vemAntes : compare(formData.dtInicio, previdencia.dtFim) > 0;
        const mesmaPrevidencia = previdencia.previdenciaComplementar.id === formData.previdenciaComplementar.id;

        if (periodoNaoCadastrado) {
          const mesmoAno = format(toDate(formData.dtInicio), 'YYYY') === format(toDate(previdencia.dtInicio), 'YYYY');
          naturezaEventoDiferente = mesmaPrevidencia && mesmoAno && previdencia.evento.naturezaEvento !== formData.evento.naturezaEvento;
        } else {
          previdenciaJaCadastrada = mesmaPrevidencia;
          eventoJaCadastrado = !mesmaPrevidencia && previdencia.evento.id === formData.evento.id;
        }
      });

      if (previdenciaJaCadastrada) {
        erros.push('Previdncia j est relacionada ao vnculo no perodo informado.');
      }
      if (eventoJaCadastrado) {
        erros.push('Evento j est relacionado ao vnculo a outra previdncia no perodo informado.');
      }
      if (naturezaEventoDiferente) {
        erros.push('Natureza do Evento no  a mesma j relacionada a esse previdncia no ano informado.');
      }
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <PrevidenciaComplementarIS
            id="previdenciaComplementar"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Previdncia Complementar"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <EventoIS
            id="evento"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Evento"
            isPrependLabel={true}
            naturezasValidas={naturezasValidas}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtInicio"
            label="Data Inicial"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
        <Col>
          <InputGroupInline id="dtFim" label="Data Final" formProps={formProps} isPrependLabel={true} type={InputType.DATE} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoPrevidenciaComplementarEditView;
