import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Button, Card, CardBody, CardHeader, Col, Input, InputGroup, InputGroupAddon, ListGroup, ListGroupItem, Row, Table } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  Box,
  decimalFormatter,
  DetailEditForm,
  DetailEditViewFormProps,
  InputGroupInline,
  InputType,
  isEmpty,
  onlyNumber,
  parseLastDay,
  parseRefToDate,
  Tab,
  TabGroup,
  toDate,
} from 'summer';
import { DiferencialAcordoConvencaoDissidio } from '../../../../models/dtos/mfp/diferencialAcordoConvencaoDissidio';
import { DiferencialAcordoConvencaoDissidioMensal } from '../../../../models/dtos/mfp/diferencialAcordoConvencaoDissidioMensal';
import { Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { SIM_NAO_ENUM_VALUE } from '../../../../models/enumerated/mfp/simNaoEnum';
import {
  OUTRAS_VERBSA_DE_NATUREZA_SALARIAL_OU_NAO_SALARIAL_DEVIDAS_APOS_DESLIGAMENTO,
  TIPO_INSTRUMENTO_SITUACAO_ENSEJADORA_REMUNERACAO_ENUM_VALUES,
} from '../../../../models/enumerated/mfp/tipoInstrumentoSituacaoEnsejadoraRemuneracaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import { getIntervaloReferencia, getMaxRef, getMinRef, getNextRef, promisedAlert } from '../../../../utilities/vinculo.util';
import EventoIS from '../../../components/mfp/eventoIS/EventoIS';

interface ScreenFields {
  filtroAno?: string;
  referenciaInicialPagamento?: string;
  referenciaFinalPagamento?: string;
  valorPagamento?: string;
}

const VinculoDiferencialAcordoEditView: FC<DetailEditViewFormProps<DiferencialAcordoConvencaoDissidio>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<DiferencialAcordoConvencaoDissidio & ScreenFields>({
    defaultValues: { pagamentos: [], ...props.selectedItem },
    mode: 'onBlur',
  });

  const [tableData, setTableData] = useState<DiferencialAcordoConvencaoDissidioMensal[]>([]);
  const [selectedIndex, setSelectedIndex] = useState(null);

  useEffect(
    () => {
      const ref = globalReducer.globalParameter.empresaMFP.referencia;
      setTimeout(() => {
        formProps.register({ name: 'pagamentos' });
        formProps.setValue([
          { filtroAno: ref.substr(0, 4) },
          { referenciaInicialPagamento: ref },
          { referenciaFinalPagamento: ref },
          { competencia: props.selectedItem ? props.selectedItem.competencia || ref : ref },
          { valorPagamento: 0 },
        ]);
        filtrarAno();
      }, 500);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const filtrarAno = () => {
    const values = formProps.getValues();
    const pagamentos = values.pagamentos.filter(item => item.referencia.substr(0, 4) === values.filtroAno);
    pagamentos.sort((a, b) => a.referencia.localeCompare(b.referencia));
    setTableData(pagamentos);
  };

  const referenciaInicialChange = () => formProps.setValue('referenciaFinalPagamento', formProps.getValues().referenciaInicialPagamento);

  const lancarPagamentos = () => {
    const erros = [];
    const values = formProps.getValues();

    const refInicial = values.referenciaInicialPagamento;
    const refFinal = values.referenciaFinalPagamento;

    if (isEmpty(refInicial)) {
      erros.push('Referncia Inicial - Campo no pode estar em branco.');
    } else {
      const mesRefInicial = parseInt(refInicial.substr(4), 10);
      if (mesRefInicial > 12 || mesRefInicial < 1) {
        erros.push('Referncia Inicial - Ms invlido.');
      }
    }

    if (isEmpty(refFinal)) {
      erros.push('Referncia Final - Campo no pode estar em branco.');
    } else {
      const mesRefFinal = parseInt(refFinal.substr(4), 10);
      if (mesRefFinal > 12 || mesRefFinal < 1) {
        erros.push('Referncia Final - Ms invlido.');
      }
    }

    if (isEmpty(values.valorPagamento) || parseFloat(values.valorPagamento) <= 0) {
      erros.push(' necessrio informar o valor do pagamento.');
    }

    if (!isEmpty(refInicial) && !isEmpty(refFinal) && parseInt(refInicial, 10) > parseInt(refFinal, 10)) {
      erros.push('Referncia final no pode ser menor que a inicial.');
    }

    if (erros.length > 0) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: (
          <>
            <p>Existem informaes invlidas.</p>
            <ListGroup variant="flush">
              {erros.map(item => (
                <ListGroupItem key={item}>{item}</ListGroupItem>
              ))}
            </ListGroup>
          </>
        ),
        type: AlertType.ERROR,
      });
    } else {
      const referencias = getIntervaloReferencia(refInicial, refFinal);
      const valor = parseFloat(values.valorPagamento);
      const pagamentos = values.pagamentos.slice();

      referencias.forEach(referencia => {
        const existe = pagamentos.find((item, index) => {
          let result = false;

          if (item.referencia === referencia) {
            pagamentos[index].valor = valor;
            result = true;
          }

          return result;
        });

        if (existe === undefined) {
          pagamentos.push({ referencia, valor });
        }
      });

      const nextRef = getNextRef(getMaxRef(pagamentos));

      formProps.setValue([
        { pagamentos },
        { filtroAno: refFinal.substr(0, 4) },
        { valorPagamento: 0 },
        { referenciaInicialPagamento: nextRef },
        { referenciaFinalPagamento: nextRef },
      ]);
      setSelectedIndex(null);
      filtrarAno();
    }
  };

  const editarPagamentos = () => {
    if (selectedIndex === null) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Selecione o item que deseja editar',
        type: AlertType.INFORMATION,
      });
    } else {
      const item = tableData[selectedIndex];
      formProps.setValue([
        { referenciaInicialPagamento: item.referencia },
        { referenciaFinalPagamento: item.referencia },
        { valorPagamento: item.valor },
      ]);
    }
  };

  const removerPagamentos = () => {
    if (selectedIndex === null) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Selecione o item que deseja remover',
        type: AlertType.INFORMATION,
      });
    } else {
      const item = tableData[selectedIndex];
      promisedAlert('Deseja mesmo remover este item?').then(() => {
        const values = formProps.getValues();
        const pagamentos = values.pagamentos.filter(pagamento => pagamento.referencia !== item.referencia);
        const nextRef = getNextRef(getMaxRef(pagamentos));
        formProps.setValue([{ pagamentos }, { referenciaInicialPagamento: nextRef }, { referenciaFinalPagamento: nextRef }]);
        setSelectedIndex(null);
        filtrarAno();
      });
    }
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (formData.pagamentos.length === 0) {
      erros.push('Pelo menos um pagamento deve ser informado');
    }

    if (
      !isEmpty(formData.tipoInstrumentoSituacaoEnsejadoraRemuneracao) &&
      formData.tipoInstrumentoSituacaoEnsejadoraRemuneracao !== OUTRAS_VERBSA_DE_NATUREZA_SALARIAL_OU_NAO_SALARIAL_DEVIDAS_APOS_DESLIGAMENTO.value &&
      isEmpty(formData.dataAssinaturaAcordo)
    ) {
      erros.push('Data da Assinatura - Campo no pode estar em branco para o Tipo ou Situao selecionada.');
    }

    if (existeMesmaCompetenciaEEvento(formData)) {
      erros.push('Esta competncia j foi registrada');
    }

    if (!isEmpty(formData.competencia) && !isEmpty(formData.dataAssinaturaAcordo)) {
      const inicioCompetencia = parseRefToDate(formData.competencia, 1);
      const finalCompetencia = parseLastDay(formData.competencia);
      const dataAssinaturaAcordo = toDate(formData.dataAssinaturaAcordo);
      if (dataAssinaturaAcordo < inicioCompetencia || dataAssinaturaAcordo > finalCompetencia) {
        erros.push('A Data de Assinatura precisa estar dentro do perodo da Competncia');
      }
    }

    if (formData.pagamentos.length > 0 && !isEmpty(formData.dataInicialEfeito)) {
      const minRef = getMinRef(formData.pagamentos);
      const dataEfeito = toDate(formData.dataInicialEfeito);
      const minDataEfeito = parseRefToDate(minRef, 1);
      if (dataEfeito < minDataEfeito) {
        erros.push('A Data do Efeito no pode ser inferior a primeira referncia de pagamento');
      }
    }

    return erros;
  };

  const existeMesmaCompetenciaEEvento = formData => {
    const vinculo: Vinculo = props.parentFormProps.getValues();
    const diferencial = vinculo.diferenciaisAcordoConvencaoDissidio.find(item => item.competencia === formData.competencia);
    return diferencial === undefined || diferencial.id !== props.selectedItem.id;
  };

  const selectIndex = index => {
    return () => {
      setSelectedIndex(index);
    };
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <EventoIS id="evento" formProps={{ ...formProps, validation: { required: true } }} label="Evento" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <TabGroup formProps={formProps}>
            <Tab title="Dados">
              <Row>
                <Col>
                  <InputGroupInline
                    formProps={formProps}
                    isPrependLabel={true}
                    id="competencia"
                    label="Competncia"
                    type={InputType.REFERENCE}
                    tooltipProps={{
                      message:
                        'Competncia em que  devida a obrigao de pagar os efeitos remuneratrios de lei, acordo coletivo, ' +
                        'conveno coletiva ou sentena normativa',
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    formProps={formProps}
                    isPrependLabel={true}
                    id="dataAssinaturaAcordo"
                    label="Data da Assinatura"
                    type={InputType.DATE}
                    tooltipProps={{
                      message:
                        'Data da Assinatura do acordo, conveno coletiva, sentena normativa ou da converso da ' +
                        'licena sade em acidente de trabalho',
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    id="tipoInstrumentoSituacaoEnsejadoraRemuneracao"
                    label="Tipo ou Situao"
                    formProps={{ ...formProps, validation: { required: true } }}
                    isPrependLabel={true}
                    placeholder="Selecione o Tipo ou Situao"
                    inputDropDownProps={{ optionsProvider: TIPO_INSTRUMENTO_SITUACAO_ENSEJADORA_REMUNERACAO_ENUM_VALUES }}
                    type={InputType.DROP_DOWN}
                    tooltipProps={{
                      message: 'Tipo do instrumento ou situao ensejadora da remunerao relativa a perodos de apurao anteriores',
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    formProps={formProps}
                    isPrependLabel={true}
                    id="dataInicialEfeito"
                    label="Data do Efeito"
                    type={InputType.DATE}
                    tooltipProps={{
                      message: 'Data a partir da qual o instrumento ou legislao mencionada em Tipo/Situao passa a produzir efeitos',
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    id="indicadorRemuneracaoRelativaNaturezaSalarial"
                    label="Remunerao Devida pela Empresa Sucessora?"
                    formProps={{ ...formProps, validation: { required: true } }}
                    isPrependLabel={true}
                    inputDropDownProps={{ optionsProvider: SIM_NAO_ENUM_VALUE }}
                    placeholder="Selecione"
                    type={InputType.DROP_DOWN}
                    tooltipProps={{
                      message:
                        'Indica se as remuneraes so relativas a verbas de natureza salarial ou no salarial devidas pela ' +
                        'empresa sucessora a empregados desligados ainda na sucedida',
                    }}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <InputGroupInline
                    formProps={{ ...formProps, validation: { required: true, maxLength: 255 } }}
                    isPrependLabel={true}
                    id="descricao"
                    label="Descrio"
                    type={InputType.TEXT_AREA}
                    tooltipProps={{
                      message: 'Descrio do instrumento ou situao que originou o pagamento das verbas relativas a perodos anteriores',
                    }}
                  />
                </Col>
              </Row>
            </Tab>
            <Tab title="Pagamentos">
              <Row>
                <Col>
                  <Box title="Lanamento">
                    <Row>
                      <Col>
                        <InputGroupInline
                          formProps={{ ...formProps, validation: { required: true } }}
                          isPrependLabel={true}
                          id="referenciaInicialPagamento"
                          label="Referncia Inicial"
                          type={InputType.REFERENCE}
                          onBlur={referenciaInicialChange}
                        />
                      </Col>
                      <Col>
                        <InputGroupInline
                          formProps={{ ...formProps, validation: { required: true } }}
                          isPrependLabel={true}
                          id="referenciaFinalPagamento"
                          label="Referncia Final"
                          type={InputType.REFERENCE}
                        />
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <InputGroupInline
                          formProps={{ ...formProps, validation: onlyNumber(0, 100) }}
                          isPrependLabel={true}
                          id="valorPagamento"
                          label="Valor"
                        />
                      </Col>
                      <Col md={4} className="text-right">
                        <Button color="success" size="sm" style={{ marginTop: 10 }} onClick={lancarPagamentos}>
                          <span className="btn-wrapper--icon">
                            <FontAwesomeIcon icon={['fas', 'plus']} />
                          </span>
                          <span className="btn-wrapper--label">Lanar</span>
                        </Button>
                      </Col>
                    </Row>
                  </Box>
                </Col>
              </Row>
              <Row>
                <Col>
                  <Card className="box-card">
                    <CardHeader className="font-weight-bold bg-light">
                      <Row>
                        <Col style={{ marginTop: 5 }}>Pagamentos cadastrados</Col>
                        <Col>
                          <InputGroup size="sm">
                            <InputGroupAddon addonType="prepend">Filtrar Ano</InputGroupAddon>
                            <Input type="number" bsSize="sm" innerRef={formProps.register} name="filtroAno" id="filtroAno" onChange={filtrarAno} />
                          </InputGroup>
                        </Col>
                        <Col className="text-right">
                          <Button id="editDiferencial" size="sm" color="link" className="text-warning" onClick={editarPagamentos}>
                            <FontAwesomeIcon icon="pen" />
                          </Button>
                          <Button id="removeDiferencial" size="sm" color="link" className="text-danger" onClick={removerPagamentos}>
                            <FontAwesomeIcon icon="trash" />
                          </Button>
                        </Col>
                      </Row>
                    </CardHeader>
                    <CardBody>
                      <Table size="sm" striped={true} bordered={true} hover={true} responsive={true}>
                        <thead>
                          <tr>
                            <th>&nbsp;</th>
                            <th>Referncia</th>
                            <th>Valor</th>
                          </tr>
                        </thead>
                        <tbody>
                          {tableData.map((item, index) => (
                            <tr key={index} onClick={selectIndex(index)} onDoubleClick={editarPagamentos}>
                              <td style={{ width: 30 }}>
                                <Input
                                  type="radio"
                                  name="itemSelecionadoValoresDiferencial"
                                  value={index}
                                  checked={selectedIndex === index}
                                  readOnly={true}
                                  style={{ marginLeft: 3, marginTop: -6 }}
                                />
                              </td>
                              <td style={{ width: 110 }} className="text-center">
                                {item.referencia}
                              </td>
                              <td>R$ {decimalFormatter(item.valor, null)}</td>
                            </tr>
                          ))}
                        </tbody>
                      </Table>
                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </Tab>
          </TabGroup>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoDiferencialAcordoEditView;
