/* eslint-disable react-hooks/exhaustive-deps */
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, onlyNumber } from 'summer';
import { VinculoValeTransporte } from '../../../../models/dtos/mfp/vinculoValeTransporte';
import {
  DIAS_UTEIS,
  INFORMAR_NRO_DIAS,
  INFORMAR_QUANTIDADE,
  TIPO_CALCULO_VALE_TRANSPORTE_ENUM_VALUES,
} from '../../../../models/enumerated/mfp/tipoCalculoValeTransporteEnum';
import EventoIS from '../../../components/mfp/eventoIS/EventoIS';
import ValeTransporteIS from '../../../components/mfp/valeTransporteIS/ValeTransporteIS';

const VinculoValeTransporteEditView: FC<DetailEditViewFormProps<VinculoValeTransporte>> = props => {
  const formProps = useForm<VinculoValeTransporte>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const [labelQuantidade, setLabelQuantidade] = useState('Quantidade por Turno');
  const [tooltipQuantidade, setTooltipQuantidade] = useState(null);
  const [diasCalculoRequired, setDiasCalculoRequired] = useState(false);
  const [diasCalculoDisabled, setDiasCalculoDisabled] = useState(true);

  const ajustarTelaTipoCalculoVT = value => {
    let tooltip = null;

    const diasCalculoReq = value === INFORMAR_NRO_DIAS.value;
    const informarQuantidade = value === INFORMAR_QUANTIDADE.value;
    const informarQuantidadePorDia = value === DIAS_UTEIS.value || value === INFORMAR_NRO_DIAS.value;

    if (informarQuantidadePorDia) {
      setLabelQuantidade('Quantidade por Dia');
    } else if (informarQuantidade) {
      setLabelQuantidade('Quantidade Total');
    } else {
      setLabelQuantidade('Quantidade por Turno');
      tooltip = { message: 'Quantidade de vales por turno. O cadastro do turno  efetuado na manuteno do quadro de horrio' };
    }

    setTooltipQuantidade(tooltip);
    setDiasCalculoRequired(diasCalculoReq);
    setDiasCalculoDisabled(!diasCalculoReq);
  };

  useEffect(() => {
    setTimeout(() => {
      if (props.selectedItem && props.selectedItem.tipoCalculo) {
        ajustarTelaTipoCalculoVT(props.selectedItem.tipoCalculo);
      }
    }, 700);
  }, []);

  useEffect(() => {
    if (diasCalculoDisabled) {
      formProps.triggerValidation('diasCalculo');
    }
  }, [diasCalculoDisabled]);

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <ValeTransporteIS
            id="valeTransporte"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Vale Transporte"
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoCalculo"
            label="Tipo de Clculo"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            placeholder="Selecione o Tipo de Clculo"
            inputDropDownProps={{ optionsProvider: TIPO_CALCULO_VALE_TRANSPORTE_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            onChange={ajustarTelaTipoCalculoVT}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <EventoIS id="evento" formProps={{ ...formProps, validation: { required: true } }} label="Evento" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="quantidade"
            label={labelQuantidade}
            formProps={{ ...formProps, validation: { required: true, ...onlyNumber(1, 4), max: 9999 } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
            tooltipProps={tooltipQuantidade}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="diasCalculo"
            label="Dias de Clculo"
            formProps={{ ...formProps, validation: { required: diasCalculoRequired, ...onlyNumber(1, 2), max: 31 } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
            disabled={diasCalculoDisabled}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoValeTransporteEditView;
