import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { add, compare, DetailEditForm, DetailEditViewFormProps, formatDate, InputGroupInline, InputType, isEmpty, onlyNumber, toDate } from 'summer';
import { VinculoExameOcupacional } from '../../../../models/dtos/mfp/vinculoExameOcupacional';
import { ADMISSIONAL, DEMISSIONAL, TIPO_EXAME_MEDICO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoExameMedicoEnum';
import { getItemAtual } from '../../../../utilities/vinculo.util';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const VinculoExameOcupacionalEditView: FC<DetailEditViewFormProps<VinculoExameOcupacional>> = props => {
  const formProps = useForm<VinculoExameOcupacional>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'nroOrdem' });

        const value = formProps.getValues();

        if (isEmpty(value.nroOrdem)) {
          const parentValues = props.parentFormProps.getValues();
          if (parentValues.examesOcupacionais.length > 0) {
            formProps.setValue('nroOrdem', getItemAtual(parentValues.examesOcupacionais).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const dtExameChange = () => {
    const values = formProps.getValues();
    if (!isEmpty(values.dtExame)) {
      formProps.setValue('dtVencimentoExame', formatDate(add(add(toDate(values.dtExame), 1, 'year'), -1, 'day')));
    }
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData) && !isEmpty(formData.dtVencimentoExame) && compare(formData.dtExame, formData.dtVencimentoExame) >= 0) {
      erros.push('A data do Exame no pode ser maior ou igual a data do Vencimento do Exame.');
    }

    parentData.examesOcupacionais.forEach(exame => {
      if (exame.nroOrdem !== formData.nroOrdem) {
        if (!isEmpty(formData.tipoExame)) {
          if (formData.tipoExame === exame.tipoExame && (ADMISSIONAL.value === formData.tipoExame || DEMISSIONAL.value === formData.tipoExame)) {
            erros.push('Somente um Exame do tipo Admissional ou Demissional pode ser cadastrado.');
          }
        }
        if (!isEmpty(formData.dtExame) && compare(formData.dtExame, exame.dtExame) === 0) {
          erros.push('A Data do Exame j est cadastrada.');
        }
      }
    });

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="tipoExame"
            label="Tipo de Exame"
            formProps={formProps}
            isPrependLabel={true}
            inputDropDownProps={{ optionsProvider: TIPO_EXAME_MEDICO_ENUM_VALUES }}
            type={InputType.DROP_DOWN}
            placeholder="Selecione o Tipo de Exame"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtExame"
            label="Data do Exame"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
            onBlur={dtExameChange}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtVencimentoExame"
            label="Vencimento do Exame"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="codigoExame"
            label="Cdigo do Exame"
            formProps={{ ...formProps, validation: { maxLength: 17 } }}
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="cnpjLaboratorio" label="CNPJ do Laboratrio" formProps={formProps} isPrependLabel={true} type={InputType.CNPJ} />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS id="ufMedicoCRM" formProps={formProps} label="UF da CRM do Mdico" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="crm" label="CRM do Mdico" formProps={{ ...formProps, validation: onlyNumber(0, 10) }} isPrependLabel={true} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoExameOcupacionalEditView;
