import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/banco';

const BancoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'nome',
            text: 'Nome',
          },
          {
            dataField: 'site',
            text: 'Site',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'BancoSearchView',
      }}
    />
  );
};

export default BancoIS;
