import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/cargo';

const CargoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'descricao',
            text: 'Descrio',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'AtividadeEconomicaSearchView',
      }}
    />
  );
};

export default CargoIS;
