import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Col, Row } from 'reactstrap';
import {
  compare,
  currentDate,
  DetailEditForm,
  DetailEditViewFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  parseDateToRef,
  parseLastDay,
} from 'summer';
import { VinculoCargo } from '../../../../models/dtos/mfp/vinculoCargo';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import { getItemAtual } from '../../../../utilities/vinculo.util';
import CargoIS from '../../../components/mfp/cargo/CargoIS';

const VinculoCargoEditView: FC<DetailEditViewFormProps<VinculoCargo>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<VinculoCargo>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const dtAtual = currentDate();
  const refAtual = parseDateToRef(dtAtual);

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'nroOrdem' });

        const values = formProps.getValues();

        if (isEmpty(values.nroOrdem)) {
          const parentValues = props.parentFormProps.getValues();
          const refGlobal = globalReducer.globalParameter.empresaMFP.referencia;

          formProps.setValue('dataAlteracao', formatDate(refAtual === refGlobal ? dtAtual : parseLastDay(refGlobal)));

          if (parentValues.cargos.length > 0) {
            formProps.setValue('nroOrdem', getItemAtual(parentValues.cargos).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }
      }, 500);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (currentItem, _, parentData) => {
    const erros = [];

    if (compare(currentItem.dataAlteracao, parentData.dataAdmissao) < 0) {
      erros.push('A Data de alterao no pode ser inferior  Data de Admisso');
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <CargoIS id="cargo" formProps={{ ...formProps, validation: { required: true } }} isPrependLabel={true} label="Cargo" />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dataAlteracao"
            label="Data de Alterao"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="motivo"
            label="Motivo"
            formProps={{ ...formProps, validation: { required: true, maxLength: 1000 } }}
            isPrependLabel={true}
            type={InputType.TEXT_AREA}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoCargoEditView;
