import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import {
  AlertType,
  CALIMA_DANGER,
  CALIMA_ORANGE,
  ErroList,
  InputGroupInline,
  InputType,
  isEmpty,
  parseDateToRef,
  ProcessBar,
  SearchExpression,
} from 'summer';
import { TransferenciaVinculo } from '../../../../models/dtos/mfp/transferenciaVinculo';
import { Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { AFASTAMENTO_ENUM_VALUES, AFASTAMENTO_TRANSFERENCIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/afastamentoEnum';
import { SEM_VINCULO_ESOCIAL_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';
import { ESOCIAL_MOTIVO_DESLIGAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/eSocialMotivoDesligamentoEnum';
import { transferirVinculo } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, toggleHeaderDrawer } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import EsocialUtil from '../../../../utilities/esocial.util';
import { promisedAlert } from '../../../../utilities/vinculo.util';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';
import QuadroHorarioIS from '../../../components/mfp/quadroHorarioIS/QuadroHorarioIS';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';

interface VinculoTransferenciaModalViewProps {
  onClose?: () => void;
  vinculo?: Vinculo;
}

const VinculoTransferenciaModalView: FC<VinculoTransferenciaModalViewProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const formProps = useForm<TransferenciaVinculo>({ defaultValues: { vinculo: props.vinculo }, mode: 'onBlur' });

  const dispatch = useDispatch();

  const [showInfoMesmaRaizCNPJ, setShowInfoMesmaRaizCNPJ] = useState(false);
  const [showInfoRaizCNPJDiferente, setShowInfoRaizCNPJDiferente] = useState(false);
  const [showInfoTransferenciaEmpresa, setShowInfoTransferenciaEmpresa] = useState(false);
  const [disabledQuadroHorario, setDisabledQuadroHorario] = useState(true);
  const [podeInformarMotivoEsocial, setPodeInformarMotivoEsocial] = useState(false);
  const [isNotVinculoTSVE, setIsNotVinculoTSVE] = useState(true);
  const [tooltip, setTooltip] = useState(null);
  const [quadroHorarioSearchExpression, setQuadroHorarioSearchExpression] = useState<SearchExpression>(null);

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('usuario', globalReducer.globalParameter.usuario);
        formProps.setValue('empresaAtual', globalReducer.globalParameter.empresa);
        setIsNotVinculoTSVE(!EnumUtil.contains(SEM_VINCULO_ESOCIAL_VALUES, props.vinculo.categoria));
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const getQuadroSearchExpressions = () => {
    const searchExpressions: SearchExpression[] = [];
    if (quadroHorarioSearchExpression) {
      searchExpressions.push(quadroHorarioSearchExpression);
    }
    return searchExpressions;
  };

  const empresaChange = value => {
    formProps.setValue([{ motivoEsocial: null }, { quadroHorario: null }]);

    setDisabledQuadroHorario(isEmpty(value));

    if (isNotVinculoTSVE && value) {
      if (value.empresaMFP) {
        let caracterizaTransferencia = true;

        if (globalReducer.globalParameter.empresa.cnpj) {
          const raizCnpjOrigem = globalReducer.globalParameter.empresa.cnpj.slice(0, 8);
          const raizCnpjDestino = value.cnpj.slice(0, 8);
          caracterizaTransferencia = raizCnpjOrigem !== raizCnpjDestino;
        } else {
          setShowInfoTransferenciaEmpresa(true);
        }

        formProps.setValue('caracterizaTransferencia', caracterizaTransferencia);

        setPodeInformarMotivoEsocial(caracterizaTransferencia);
        setShowInfoRaizCNPJDiferente(caracterizaTransferencia);
        setShowInfoMesmaRaizCNPJ(!caracterizaTransferencia);

        setQuadroHorarioSearchExpression({
          fieldName: 'empresaMFP.id',
          value: value.empresaMFP.id,
        });
      } else {
        promisedAlert(
          'A empresa selecionada no est configurada para o Mdulo Folha de Pagamento. Para configur-la acesse Manuteno > Configuraes > Empresa',
          AlertType.INFORMATION
        );
      }
    }
  };

  const afastamentoChange = value => {
    setTooltip(value ? { message: EsocialUtil.getToolTipFmt(EnumUtil.find(AFASTAMENTO_ENUM_VALUES, value)) } : null);
  };

  const validateExtras = formData => {
    const erros = [];

    if (formData.empresaDestino) {
      if (formData.empresaDestino.id === globalReducer.globalParameter.empresa.id) {
        erros.push('Empresa selecionada  a atual do vnculo.');
      } else {
        if (formData.empresaDestino.empresaMFP) {
          const selecionadaPossuiGrupoEvento =
            formData.empresaDestino.grupoEmpresa &&
            formData.empresaDestino.grupoEmpresa.grupoEmpresaConfig &&
            formData.empresaDestino.grupoEmpresa.grupoEmpresaConfig.grupoEvento;

          const atualPossuiGrupoEvento =
            globalReducer.globalParameter.empresa.grupoEmpresa &&
            globalReducer.globalParameter.empresa.grupoEmpresa.grupoEmpresaConfig &&
            globalReducer.globalParameter.empresa.grupoEmpresa.grupoEmpresaConfig.grupoEvento;

          if (selecionadaPossuiGrupoEvento && atualPossuiGrupoEvento) {
            if (
              formData.empresaDestino.grupoEmpresa.grupoEmpresaConfig.grupoEvento.id !==
              globalReducer.globalParameter.empresa.grupoEmpresa.grupoEmpresaConfig.grupoEvento.id
            ) {
              erros.push('Empresa selecionada deve ter o mesmo grupo de eventos da empresa atual.');
            }
          } else {
            if (!atualPossuiGrupoEvento) {
              erros.push('Empresa atual no possui grupo de eventos configurado.');
            }
            if (!selecionadaPossuiGrupoEvento) {
              erros.push('Empresa selecionada no possui grupo de eventos configurado.');
            }
          }
        } else {
          erros.push('Empresa selecionada no est configurada para o Mdulo Folha de Pagamento.');
        }
      }
    }

    if (formData.data && parseInt(parseDateToRef(formData.data), 10) <= parseInt(globalReducer.globalParameter.empresaMFP.referencia, 10)) {
      erros.push('Data da transferncia no pode ser menor ou igual a referncia atual.');
    }

    return erros;
  };

  const onConfirm = async () => {
    const check = await formProps.triggerValidation();
    if (check) {
      const values = formProps.getValues();
      const erros = validateExtras(values);
      if (erros.length > 0) {
        promisedAlert(<ErroList erros={erros} />, AlertType.ERROR);
      } else {
        transferirVinculo(values, {
          thenFunction: () => {
            promisedAlert(
              'O sistema est realizando a transferncia. Uma notificao ser exibida quando estiver finalizado.',
              AlertType.INFORMATION
            ).then(() => {
              if (!globalReducer.headerDrawerToggle) {
                dispatch(toggleHeaderDrawer());
              }
              props.onClose();
            });
          },
        });
      }
    }
  };

  return (
    <Modal centered={true} backdrop="static" isOpen={true} toggle={props.onClose} size="lg">
      <ModalHeader toggle={props.onClose}>Transferncia de Vnculo</ModalHeader>
      <ModalBody>
        <Row>
          <Col className="text-center">
            <b>Ateno</b>
            <br />
            <p>
              Os vnculos que se enquadram nas categorias de TSVE (Trabalhador Sem Vnculo de Emprego) do eSocial
              <span style={{ color: CALIMA_DANGER }}> NO</span> geram eventos pendentes ao efetuar a tranferncia.
            </p>
          </Col>
        </Row>
        <Row>
          <Col>
            <VinculoIS
              id="vinculo"
              formProps={{ ...formProps, validation: { required: true } }}
              label="Vnculo"
              isPrependLabel={true}
              tooltipProps={{ message: 'Vnculo a ser transferido' }}
              disabled={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <EmpresaIS
              id="empresaDestino"
              formProps={{ ...formProps, validation: { required: true } }}
              label="Empresa"
              isPrependLabel={true}
              onChange={empresaChange}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="data"
              label="Data de Transferncia"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <QuadroHorarioIS
              id="quadroHorario"
              formProps={{ ...formProps, validation: { required: isNotVinculoTSVE } }}
              label="Quadro de Horrio"
              isPrependLabel={true}
              disabled={disabledQuadroHorario}
              searchExpressions={getQuadroSearchExpressions}
              tooltipProps={{ message: 'Selecione o Quadro de Horrios que ser utilizado na empresa de destino.' }}
            />
            <p className="text-center" style={{ color: CALIMA_DANGER }}>
              (O Quadro de Horrios  necessrio para a gerao dos eventos pendentes do eSocial)
            </p>
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="motivo"
              label="Motivo SEFIP"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Selecione o Motivo SEFIP"
              inputDropDownProps={{ optionsProvider: AFASTAMENTO_TRANSFERENCIA_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
              onChange={afastamentoChange}
              tooltipProps={tooltip}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="motivoEsocial"
              label="Motivo eSocial"
              formProps={{ ...formProps, validation: { required: podeInformarMotivoEsocial } }}
              isPrependLabel={true}
              placeholder="Selecione o Motivo eSocial"
              inputDropDownProps={{ optionsProvider: ESOCIAL_MOTIVO_DESLIGAMENTO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
              disabled={!podeInformarMotivoEsocial}
              tooltipProps={{ message: 'Motivo de Transferncia do eSocial.' }}
            />
          </Col>
        </Row>
        {showInfoMesmaRaizCNPJ && (
          <Row>
            <Col className="text-center">
              <p style={{ color: CALIMA_ORANGE }}>Transferncia Entre Matriz e Filial (mesma raiz CNPJ)</p>
            </Col>
          </Row>
        )}
        {showInfoRaizCNPJDiferente && (
          <Row>
            <Col className="text-center">
              <p style={{ color: CALIMA_ORANGE }}>Transferncia Entre Empresas {!showInfoTransferenciaEmpresa && '(raiz CNPJ diferente)'}</p>
            </Col>
          </Row>
        )}
      </ModalBody>
      <ModalFooter>
        <ProcessBar handleClose={props.onClose} handleSubmit={onConfirm} btnSubmitIcon="check" btnCloseLabel="Cancel" btnSubmitLabel="Confirmar" />
      </ModalFooter>
    </Modal>
  );
};

export default VinculoTransferenciaModalView;
