import React, { FC, useEffect, useRef } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import {
  Box,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditFormRefProps,
  EditViewProps,
  enumColumn,
  getSelectedItem,
  InputGroupInline,
  InputType,
  OrderType,
  ProcessBar,
  WindowComponent,
  Wizard,
  WizardActionFinishProps,
  WizardPage,
} from 'summer';
import { CLASS_NAME, Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { TIPO_EXAME_MEDICO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoExameMedicoEnum';
import { getItemAtual, loadVinculo, registrarListasVinculo } from '../../../../utilities/vinculo.util';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';
import VinculoExameOcupacionalEditView from './VinculoExameOcupacionalEditView';

interface ExameEditViewProps extends EditViewProps<Vinculo> {
  onDockSubmit?: (data: any) => void;
  openViaDock?: boolean;
}

interface ScreenFields {
  vinculo?: Vinculo;
  ultimoExameVencimento?: string;
}

const ExameEditView: FC<ExameEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<Vinculo & ScreenFields>({ defaultValues: { examesOcupacionais: [], ...selectedItem }, mode: 'onBlur' });
  const dispatch = useDispatch();

  const editFormRef = useRef<EditFormRefProps>();

  useEffect(
    () => {
      setTimeout(() => {
        registrarListasVinculo(formProps, ['examesOcupacionais']);
        if (selectedItem && selectedItem.id) {
          updateUltimoExameVencimento();
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const updateUltimoExameVencimento = () => {
    const values = formProps.getValues();
    if (values.examesOcupacionais.length > 0) {
      formProps.setValue('ultimoExameVencimento', getItemAtual(values.examesOcupacionais).dtVencimentoExame);
    }
  };

  const onPageChange = currentPageId => {
    if (currentPageId === 'exames') {
      loadVinculo(formProps, dispatch, () => {
        updateUltimoExameVencimento();
      });
    }
  };

  const reset = () => {
    formProps.setValue('vinculo', null);
  };

  const onCancel = () => {
    editFormRef.current.close();
  };

  const handleSubmitViaDock = () => {
    editFormRef.current.submit(false).then(res => {
      editFormRef.current.close();
      if (props.onDockSubmit) {
        props.onDockSubmit(res);
      }
    });
  };

  const exameDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'nroOrdem',
      order: OrderType.ASC,
    },
  ];

  const exameFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'dtExame',
        text: 'Data do Exame',
      },
      {
        dataField: 'dtVencimentoExame',
        text: 'Vencimento do Exame',
      },
      enumColumn(
        {
          dataField: 'tipoExame',
          text: 'Tipo de Exame',
        },
        TIPO_EXAME_MEDICO_ENUM_VALUES
      ),
    ],
    detailView: VinculoExameOcupacionalEditView,
    formProps,
    onChange: updateUltimoExameVencimento,
    propName: 'examesOcupacionais',
    title: 'Histrico de Exames',
  };

  const wizardFinishProps: WizardActionFinishProps = {
    confirmButton: {
      label: 'Salvar',
    },
    onCancel: () => {
      reset();
      onCancel();
    },
    onConfirm: () => {
      editFormRef.current.submit().then(reset);
    },
  };

  const getEditView = () => (
    <>
      <Row>
        <Col>
          <Box title="Ultimo Exame Ocupacional">
            <Row>
              <Col>
                <InputGroupInline
                  id="ultimoExameVencimento"
                  formProps={formProps}
                  label="Vencimento"
                  isPrependLabel={true}
                  type={InputType.DATE}
                  disabled={true}
                />
              </Col>
            </Row>
          </Box>
        </Col>
      </Row>
      <Row>
        <Col>
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={exameDefaultSortProps}
            {...exameFormProps}
            keyField="rowIndex"
          />
        </Col>
      </Row>
    </>
  );

  return (
    <WindowComponent title="Manuteno de Exames" wrapBody={props.openViaDock === true} wikiId={2983}>
      <EditForm ref={editFormRef} formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} externalSubmit={true}>
        {props.openViaDock === true ? (
          <div className="m-3">
            {getEditView()}
            <ProcessBar handleSubmit={handleSubmitViaDock} handleClose={onCancel} btnSubmitIcon="save" btnSubmitLabel="Salvar" />
          </div>
        ) : (
          <Wizard formProps={formProps} onAfterPageChange={onPageChange} finishProps={wizardFinishProps}>
            <WizardPage id="vinculo" title="Seleo do vnculo">
              <Row>
                <Col>
                  <VinculoIS id="vinculo" formProps={{ ...formProps, validation: { required: true } }} label="Vinculo" isPrependLabel={true} />
                </Col>
              </Row>
            </WizardPage>
            <WizardPage id="exames" title="Exames">
              {getEditView()}
            </WizardPage>
          </Wizard>
        )}
      </EditForm>
    </WindowComponent>
  );
};

export default ExameEditView;
