import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/quadroHorario';

interface QuadroHorarioISProps {
  searchExpressions?: () => SearchExpression[];
}

const QuadroHorarioIS: FC<InputProps & QuadroHorarioISProps> = props => {
  const getSearchExpressions = () => {
    const searchExpressions: SearchExpression[] = [];
    if (props.searchExpressions) {
      searchExpressions.push(...props.searchExpressions());
    }
    return searchExpressions;
  };

  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'descricao',
            text: 'Descrio',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchExpressions: getSearchExpressions,
        searchViewId: 'QuadroHorarioSearchView',
      }}
    />
  );
};

export default QuadroHorarioIS;
