import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import {
  compare,
  currentDate,
  DefaultSortProps,
  DetailEditForm,
  DetailEditViewFormProps,
  DetailManagerForm,
  DetailManagerFormProps,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  OrderType,
} from 'summer';
import { VinculoPlanoSaude } from '../../../../models/dtos/mfp/vinculoPlanoSaude';
import { getItemAtual } from '../../../../utilities/vinculo.util';
import EventoIS from '../../../components/mfp/eventoIS/EventoIS';
import PlanoSaudeIS from '../../../components/mfp/planoSaudeIS/PlanoSaudeIS';
import VinculoPlanoSaudeReembolsoEditView from './VinculoPlanoSaudeReembolsoEditView';

const VinculoPlanoSaudeEditView: FC<DetailEditViewFormProps<VinculoPlanoSaude>> = props => {
  const formProps = useForm<VinculoPlanoSaude>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        const vinculo = props.parentFormProps.getValues();

        formProps.setValue('dataAlteracao', formatDate(currentDate()));
        formProps.setValue('dtInicio', vinculo.dataAdmissao);

        if (vinculo.planos.length > 0) {
          formProps.setValue('nroOrdem', getItemAtual(vinculo.planos).nroOrdem + 1);
        } else {
          formProps.setValue('nroOrdem', 1);
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const reembolsoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'ano',
      order: OrderType.ASC,
    },
  ];

  const reembolsoFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'razaoSocial',
        text: 'Razo Social',
      },
      {
        dataField: 'valorAnoAtual',
        text: 'Valor Atual',
      },
      {
        dataField: 'valorAnoAnteriores',
        text: 'Valor Anterior',
      },
    ],
    detailView: VinculoPlanoSaudeReembolsoEditView,
    formProps,
    propName: 'reembolsos',
    title: 'Reembolsos de Planos de Sade',
  };

  const validateExtras = (formData, _, parentData) => {
    const erros = [];

    if (!isEmpty(formData.dtFim) && compare(formData.dtInicio, formData.dtFim) === 1) {
      erros.push('A Data Final deve estar em branco ou posterior  Data Inicial.');
    }

    if (compare(parentData.dataAdmissao, formData.dtInicio) === 1) {
      erros.push('A Data Inicial no pode ser anterior  Data de Admisso.');
    }

    const itemRepetido = parentData.planos.find(item => {
      let result = false;
      let prosseguir = true;

      if (!isEmpty(item.id) && !isEmpty(formData.id) && item.id === formData.id) {
        prosseguir = false;
      }

      if (prosseguir) {
        const mesmoEvento = formData.evento && item.evento && item.evento.id === formData.evento.id;
        const dtFimNoIntervalo = isEmpty(formData.dtFim) || compare(item.dtInicio, formData.dtFim) <= 0;
        const dtInicioNoIntervalo = isEmpty(item.dtFim) || compare(item.dtFim, formData.dtInicio) >= 0;
        result = mesmoEvento && dtFimNoIntervalo && dtInicioNoIntervalo;
      }

      return result;
    });

    if (itemRepetido !== undefined) {
      erros.push(
        'J existe Plano de sade configurado para o evento ' + formData.evento.label + ' pertencente ao perodo de incio e fim informado.'
      );
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <PlanoSaudeIS id="planoSaude" formProps={{ ...formProps, validation: { required: true } }} label="Plano de Sade" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <EventoIS id="evento" formProps={{ ...formProps, validation: { required: true } }} label="Evento" isPrependLabel={true} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="dtInicio"
            label="Data Inicial"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            type={InputType.DATE}
          />
        </Col>
        <Col>
          <InputGroupInline id="dtFim" label="Data Final" formProps={formProps} isPrependLabel={true} type={InputType.DATE} />
        </Col>
      </Row>
      <Row>
        <Col>
          <DetailManagerForm
            {...props}
            defaultSortDirection={OrderType.ASC}
            defaultSorted={reembolsoDefaultSortProps}
            {...reembolsoFormProps}
            keyField="rowIndex"
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoPlanoSaudeEditView;
