import React, { FC, useRef } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Col, Row } from 'reactstrap';
import {
  ColumnsProps,
  CountComparatorEnum,
  cpfFormatter,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  InputGroupInline,
  InputType,
  OperationEnum,
  OrderType,
  parseRefToDate,
  SearchDataGrid,
  SearchDataGridRefProps,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { Empresa } from '../../../../models/dtos/ger/empresa';
import { CLASS_NAME } from '../../../../models/dtos/mfp/vinculo';
import { CLASS_NAME as CLASS_NAME_VIEW, VinculoVW } from '../../../../models/dtos/mfp/vinculoVW';
import { ATIVOS, INATIVOS, PERFIL_ENUM_MALE_VALUES, TODOS } from '../../../../models/enumerated/ger/perfilEnum';
import { CATEGORIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';
import { TOTAL } from '../../../../models/enumerated/mfp/tipoResilicaoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import VinculoRemoveConfirmationView from './VinculoRemoveConfirmationView';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'matricula',
    order: OrderType.DESC,
  },
];

interface VinculoSearchViewProps extends SearchViewProps<VinculoVW> {
  apenasInativos?: boolean;
  empresasSelecionadas?: Empresa[];
}

const VinculoSearchView: FC<VinculoSearchViewProps> = props => {
  const formProps = useForm<VinculoVW & { perfil: string }>({
    defaultValues: { perfil: props.apenasInativos ? INATIVOS.value : ATIVOS.value },
    mode: 'onBlur',
  });
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const searchRef = useRef<SearchDataGridRefProps>();

  const columns: ColumnsProps[] = [
    {
      dataField: 'matricula',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Matrcula',
    },
    {
      dataField: 'nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Trabalhador',
    },
    enumColumn(
      {
        dataField: 'categoria',
        editor: true,
        filter: true,
        sort: true,
        text: 'Categoria',
      },
      CATEGORIA_ENUM_VALUES
    ),
    {
      dataField: 'dataAdmissao',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. Admisso',
    },
    {
      dataField: 'dataAfastamentoMovimentacao',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. ltimo Afastamento',
    },
    {
      dataField: 'dataAfastamentoRecisao',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. Resciso',
    },
    {
      dataField: 'cpf',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: cpfFormatter,
      sort: true,
      text: 'CPF',
    },
  ];

  const searchExpressionsFull = defaultExpressions => {
    const { referencia } = globalReducer.globalParameter.empresaMFP;
    const mainExpression: SearchExpression[] = [...defaultExpressions];
    const advancedSearch: SearchExpression[] = [];

    if (props.apenasInativos) {
      mainExpression.push({
        expressions: advancedSearch,
        fieldName: 'empresaMFP.id',
        operation: OperationEnum.IN,
        value: getIdsEmpresasMFP(),
      });
    } else {
      mainExpression.push({
        expressions: advancedSearch,
        fieldName: 'empresaMFP.id',
        value: globalReducer.globalParameter.empresaMFP.id,
      });
    }

    if (formProps.getValues().perfil !== TODOS.value) {
      const dataReferenciaEmpresa = parseRefToDate(referencia, 0);

      if (formProps.getValues().perfil === ATIVOS.value) {
        advancedSearch.push({
          alias: 'dataAfastamentoRecisao1',
          conditionalGroup: 'd0',
          countOperation: CountComparatorEnum.EQ,
          fieldName: 'dataAfastamentoRecisao',
          operation: OperationEnum.EQUAL,
          value: null,
        });

        advancedSearch.push({
          alias: 'dataAfastamentoRecisao2',
          conditionalGroup: 'd0',
          fieldName: 'dataAfastamentoRecisao',
          operation: OperationEnum.GREATER_THAN,
          value: dataReferenciaEmpresa,
        });

        advancedSearch.push({
          conditionalGroup: 'd2',
          fieldName: 'dataAfastamentoMovimentacao',
          operation: OperationEnum.EQUAL,
          value: null,
        });

        advancedSearch.push({
          conditionalGroup: 'd2',
          fieldName: 'dataAfastamentoMovimentacao',
          operation: OperationEnum.GREATER_THAN,
          value: dataReferenciaEmpresa,
        });

        advancedSearch.push({
          conditionalGroup: 'd2',
          fieldName: 'dataRetornoMovimentacao',
          operation: OperationEnum.LESS_THAN_EQUAL,
          value: dataReferenciaEmpresa,
        });

        advancedSearch.push({
          alias: 'dataAfastamentoResilicao2',
          conditionalGroup: 'd3',
          fieldName: 'dataAfastamentoResilicao',
          operation: OperationEnum.EQUAL,
          value: null,
        });

        advancedSearch.push({
          alias: 'dataAfastamentoResilicao1',
          conditionalGroup: 'd3',
          fieldName: 'dataAfastamentoResilicao',
          operation: OperationEnum.GREATER_THAN,
          value: dataReferenciaEmpresa,
        });

        advancedSearch.push({
          alias: 'tipoResilicao1',
          conditionalGroup: 'd3',
          fieldName: 'tipoResilicao',
          operation: OperationEnum.DIFERENT,
          value: TOTAL.value,
        });
      } else {
        advancedSearch.push({
          conditionalGroup: 'd0',
          fieldName: 'dataAfastamentoRecisao',
          operation: OperationEnum.LESS_THAN_EQUAL,
          value: dataReferenciaEmpresa,
        });

        advancedSearch.push({
          conditionalGroup: 'd0',
          expressions: [
            {
              conditionalGroup: 'd2',
              fieldName: 'tipoResilicao',
              operation: OperationEnum.EQUAL,
              value: TOTAL.value,
            },
          ],
          fieldName: 'dataAfastamentoRecisao',
          operation: OperationEnum.LESS_THAN_EQUAL,
          value: dataReferenciaEmpresa,
        });

        if (!props.apenasInativos) {
          advancedSearch.push({
            conditionalGroup: 'd0',
            expressions: [
              {
                conditionalGroup: 'd2',
                fieldName: 'dataRetornoMovimentacao',
                operation: OperationEnum.GREATER_THAN,
                value: dataReferenciaEmpresa,
              },
              {
                alias: 'dataRetornoMovimentacao2',
                conditionalGroup: 'd2',
                fieldName: 'dataRetornoMovimentacao',
                operation: OperationEnum.EQUAL,
                value: null,
              },
            ],
            fieldName: 'dataAfastamentoMovimentacao',
            operation: OperationEnum.LESS_THAN_EQUAL,
            value: dataReferenciaEmpresa,
          });
        }
      }
    }

    return mainExpression;
  };

  const getIdsEmpresasMFP = (): number[] => {
    return props.empresasSelecionadas.filter(it => it && it.empresaMFP).map(it => it.empresaMFP.id);
  };

  const onChangeSituacao = () => setTimeout(() => searchRef.current.notifySearchExpressionChange(), 200);

  return (
    <WindowComponent
      title={props.title || 'Manuteno de Trabalhador - Vnculo'}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={2983}
    >
      <Row>
        <Col>
          <InputGroupInline
            id="perfil"
            label="Situao"
            formProps={{ ...formProps, validation: { required: true } }}
            isPrependLabel={true}
            placeholder="Informe a situao dos trabalhadores"
            inputDropDownProps={{
              optionsProvider: PERFIL_ENUM_MALE_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
            onChange={onChangeSituacao}
          />
        </Col>
      </Row>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME_VIEW}
        extraData={getExtraData(props, 'VinculoEditView', CLASS_NAME, { removeConfirmationView: VinculoRemoveConfirmationView })}
        ref={searchRef}
        searchExpressionsFull={searchExpressionsFull}
      />
    </WindowComponent>
  );
};

export default VinculoSearchView;
