import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, isEmpty, SearchExpression } from 'summer';
import { VinculoQuadroHorario } from '../../../../models/dtos/mfp/vinculoQuadroHorario';
import { getItemAtual } from '../../../../utilities/vinculo.util';
import QuadroHorarioIS from '../../../components/mfp/quadroHorarioIS/QuadroHorarioIS';

const VinculoQuadroHorarioEditView: FC<DetailEditViewFormProps<VinculoQuadroHorario>> = props => {
  const formProps = useForm<VinculoQuadroHorario>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'nroOrdem' });

        const values = formProps.getValues();

        if (isEmpty(values.nroOrdem)) {
          const parentValues = props.parentFormProps.getValues();

          if (parentValues.quadrosHorarios.length > 0) {
            formProps.setValue('nroOrdem', getItemAtual(parentValues.quadrosHorarios).nroOrdem + 1);
          } else {
            formProps.setValue('nroOrdem', 1);
          }
        }
      }, 500);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <QuadroHorarioIS
            id="quadroHorario"
            formProps={{ ...formProps, validation: { required: true } }}
            label="Quadro de Horrio"
            isPrependLabel={true}
            inputSearchProps={{
              searchExpressions: (): SearchExpression[] => [
                {
                  fieldName: 'empresaMFP.id',
                  value: props.parentFormProps.getValues().empresaMFP.id,
                },
              ],
            }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="descricao" label="Descrio" formProps={formProps} isPrependLabel={true} />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoQuadroHorarioEditView;
