import React, { FC, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router';
import { Card } from 'reactstrap';
import { dockActions, ProcessBar, SearchViewProps, SelectModeEnum } from 'summer';
import { Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { CLASS_NAME } from '../../../../models/dtos/mfp/vinculoExample';
import VinculoSearchView from './VinculoSearchView';

interface VinculoInternalSearchViewProps {
  onSelect: (data: any) => void;
  onCancel?: () => void;
}

const VinculoInternalSearchView: FC<VinculoInternalSearchViewProps> = props => {
  const dispatch = useDispatch();
  const history = useHistory();

  const [currentSelected, setCurrentSelected] = useState(null);

  useEffect(
    () => {
      // Remove a Dock para que ela no fique em cima do contedo
      dispatch(dockActions.destroyDock());
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const searchViewProps: SearchViewProps<Vinculo> = {
    hiddenHeader: true,
    searchDataGridProps: {
      columns: [
        {
          dataField: 'matricula',
          text: 'Matrcula',
        },
        {
          dataField: 'nome',
          text: 'Trabalhador',
        },
        {
          dataField: 'categoria',
          text: 'Categoria',
        },
        {
          dataField: 'cpf',
          text: 'CPF',
        },
      ],
      dtoClassName: CLASS_NAME,
      extraData: { isModal: true, size: 'xl' },
      selectRow: {
        clickToSelect: true,
        hideSelectAll: true,
        mode: SelectModeEnum.RADIO,
        onSelect: row => {
          setCurrentSelected(row);
        },
      },
    },
  };

  const onCancel = () => {
    history.goBack();
    if (props.onCancel) {
      props.onCancel();
    }
  };

  const onConfirm = () => {
    props.onSelect(currentSelected);
    history.goBack();
  };

  return (
    <Card>
      <VinculoSearchView {...searchViewProps} />
      <div className="float-right mr-4 mb-4">
        <ProcessBar handleClose={onCancel} handleSubmit={onConfirm} btnSubmitIcon="check" btnCloseLabel="Cancel" btnSubmitLabel="Confirmar" />
      </div>
    </Card>
  );
};

export default VinculoInternalSearchView;
