import { saveAs } from 'file-saver';
import React, { FC, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { Col, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { AlertType, Checkbox, compare, ErroList, InputGroupInline, InputType, isEmpty, onlyNumber, ProcessBar, progressActions } from 'summer';
import { Reintegracao } from '../../../../models/dtos/mfp/reintegracao';
import { Vinculo } from '../../../../models/dtos/mfp/vinculo';
import { NR_LEI_ANISTIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/nrLeiAnistiaEnum';
import { ANISTIAL_LEGAL, DECISAO_JUDICIAL, TIPO_REINTEGRACAO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoReintegracaoEnum';
import { reintegrarVinculo } from '../../../../services/mfp.service';
import { fileDownload } from '../../../../services/report.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import { promisedAlert, showLoading } from '../../../../utilities/vinculo.util';
import VinculoIS from '../../../components/mfp/vinculoIS/VinculoIS';

interface VinculoReintegracaoModalViewProps {
  onClose?: () => void;
  vinculo?: Vinculo;
}

const VinculoReintegracaoModalView: FC<VinculoReintegracaoModalViewProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<Reintegracao>({ defaultValues: { vinculo: props.vinculo }, mode: 'onBlur' });

  const dispatch = useDispatch();

  const [isProcesso, setIsProcesso] = useState(false);
  const [isLeiAnistia, setIsLeiAnistia] = useState(false);

  const tipoReintegracaoChange = value => {
    const processo = value === DECISAO_JUDICIAL.value;
    const anistia = value === ANISTIAL_LEGAL.value;

    setIsProcesso(processo);
    setIsLeiAnistia(anistia);

    if (processo) {
      formProps.setValue('numeroLeiAnistia', null);
    } else if (anistia) {
      formProps.setValue('numeroProcesso', null);
    } else {
      formProps.setValue([{ numeroLeiAnistia: null }, { numeroProcesso: null }]);
    }
  };

  const completarNumeroProcesso = () => {
    formProps.setValue('numeroProcesso', formProps.getValues().numeroProcesso.padStart(20, '0'));
  };

  const validateExtras = formData => {
    const erros = [];

    if (formData.tipoReintegracao === DECISAO_JUDICIAL.value && isEmpty(formData.numeroProcesso)) {
      erros.push('Para o tipo de Reintegraco por Deciso Judicial  obrigatrio informar o nmero do processo.');
    }

    if (formData.tipoReintegracao === ANISTIAL_LEGAL.value && isEmpty(formData.numeroLeiAnistia)) {
      erros.push('Para o tipo de Reintegraco por Anistia Legal  obrigatrio informar o nmero da Lei de Anistia.');
    }

    if (!isEmpty(formData.dataEfetivoRetorno) && compare(formData.dataEfetivoRetorno, formData.vinculo.recisao.dtAfastamento) <= 0) {
      erros.push('Data do Efetivo Retorno deve ser posterior  data de desligamento do trabalhador');
    }

    if (!isEmpty(formData.dataEfetivoRetorno) && !isEmpty(formData.dataReintegracao)) {
      if (compare(formData.dataReintegracao, formData.dataEfetivoRetorno) === 1) {
        erros.push('Data da Reintegrao deve ser uma data igual ou anterior  data do efetivo retorno ao trabalho');
      }
      if (compare(formData.dataReintegracao, formData.vinculo.recisao.dtAfastamento) <= 0) {
        erros.push('Data da Reintegrao deve ser posterior  data de desligamento do trabalhador');
      }
    }

    return erros;
  };

  const onConfirm = async () => {
    const check = await formProps.triggerValidation();
    if (check) {
      const values = formProps.getValues();
      const erros = validateExtras(values);

      if (erros.length > 0) {
        promisedAlert(<ErroList erros={erros} />, AlertType.ERROR);
      } else {
        promisedAlert(
          'Ao final do processo de Reintegrao do Trabalhador a Resciso ser apagada (juntamente com seus clculos caso existam) ' +
            'esse  um processo irreversvel. Deseja Continuar?'
        ).then(() => {
          dispatch(showLoading());
          reintegrarVinculo(
            {
              empresa: { ...globalReducer.globalParameter.empresa, empresaMFP: globalReducer.globalParameter.empresaMFP },
              reintegracao: values,
              usuario: globalReducer.globalParameter.usuario,
              vinculo: values.vinculo,
            },
            {
              thenFunction: res => {
                if (res) {
                  promisedAlert(
                    'No foi possvel realizar a reintegrao do vnculo. Foi gerado um relatrio de pendncias que precisam ser ajustadas.',
                    AlertType.ERROR
                  ).then(() => {
                    fileDownload(res.split('<')[1], {
                      errorFunction: mensagem => {
                        dispatch(progressActions.hideProgress());
                        promisedAlert(mensagem, AlertType.ERROR);
                      },
                      thenFunction: response => {
                        dispatch(progressActions.hideProgress());
                        const filename = response.headers['content-disposition'].split('filename=')[1];
                        saveAs(response.data, filename);
                      },
                    });
                  });
                } else {
                  dispatch(progressActions.hideProgress());
                  promisedAlert(
                    'Reintegrao realizada com sucesso. Evento Pendente adicionado para envio ao eSocial. Para verificar os detalhes ' +
                      'do mesmo acesse: Mdulo Folha de Pagamento > eSocial > Exportao > Monitor de Eventos.',
                    AlertType.INFORMATION
                  );
                }
              },
            }
          );
        });
      }
    }
  };

  return (
    <Modal isOpen={true} centered={true} backdrop="static" toggle={props.onClose} size="lg">
      <ModalHeader toggle={props.onClose}>Reintegrao de Vnculo</ModalHeader>
      <ModalBody>
        <Row>
          <Col>
            <VinculoIS
              id="vinculo"
              formProps={{ ...formProps, validation: { required: true } }}
              label="Vnculo"
              isPrependLabel={true}
              tooltipProps={{ message: 'Vnculo a ser transferido' }}
              disabled={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoReintegracao"
              label="Tipo de Reintegrao"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Selecione o Tipo de Reintegrao"
              inputDropDownProps={{ optionsProvider: TIPO_REINTEGRACAO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
              onChange={tipoReintegracaoChange}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataReintegracao"
              label="Data Reintegrao (Efeito)"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="numeroProcesso"
              label="Nmero do Processo"
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 20), maxLength: 20 } }}
              isPrependLabel={true}
              disabled={!isProcesso}
              onBlur={completarNumeroProcesso}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="dataEfetivoRetorno"
              label="Data Efetivo Retorno"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="numeroLeiAnistia"
              label="Nmero Lei de Anistia"
              formProps={formProps}
              isPrependLabel={true}
              inputDropDownProps={{ optionsProvider: NR_LEI_ANISTIA_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
              disabled={!isLeiAnistia}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Checkbox id="remuneracaoPagaEmJuizo" formProps={formProps} label="Remuneraes/Contribuies pagas em Juizo" />
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter>
        <ProcessBar handleClose={props.onClose} handleSubmit={onConfirm} btnSubmitIcon="check" btnCloseLabel="Cancel" btnSubmitLabel="Confirmar" />
      </ModalFooter>
    </Modal>
  );
};

export default VinculoReintegracaoModalView;
