import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, isEmpty, onlyNumber } from 'summer';
import { VinculoPlanoSaudeReembolso } from '../../../../models/dtos/mfp/vinculoPlanoSaudeReembolso';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const VinculoPlanoSaudeReembolsoEditView: FC<DetailEditViewFormProps<VinculoPlanoSaudeReembolso>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<VinculoPlanoSaudeReembolso>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('ano', parseInt(globalReducer.globalParameter.empresaMFP.referencia.substr(0, 4), 10));
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = formData => {
    const erros = [];

    if (isEmpty(formData.cpf) && isEmpty(formData.cnpj)) {
      erros.push('Campo CPF ou CNPJ deve ser preenchido');
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline
            id="razaoSocial"
            label="Razo Social"
            formProps={{ ...formProps, validation: { required: true, maxLength: 250 } }}
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="cnpj" label="CNPJ" formProps={formProps} isPrependLabel={true} type={InputType.CNPJ} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="cpf" label="CPF" formProps={formProps} isPrependLabel={true} type={InputType.CPF} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="ano"
            label="ANO"
            formProps={{ ...formProps, validation: { ...onlyNumber(0, 4), min: 1930, max: 3000 } }}
            isPrependLabel={true}
            type={InputType.NUMBER}
            tooltipProps={{ message: 'Ano Calendrio' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="valorAnoAtual"
            label="Valor Ano Atual"
            formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 100) } }}
            isPrependLabel={true}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            id="valorAnoAnteriores"
            label="Valor Anos Anteriores"
            formProps={{ ...formProps, validation: { required: true, ...onlyNumber(0, 100) } }}
            isPrependLabel={true}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default VinculoPlanoSaudeReembolsoEditView;
