import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/sindicatoExample';

const SindicatoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'razaoSocial',
            text: 'Razo Social',
          },
          {
            dataField: 'cnpj',
            text: 'CNPJ',
          },
          {
            dataField: 'codigoEntidadeSindical',
            text: 'Cd. Entidade Sindical',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'SindicatoSearchView',
      }}
    />
  );
};

export default SindicatoIS;
